<template>
  <div class="page_back">
    <div class="apply_title">
      <div class="page_title">
      <div @click="toMainPage()"></div>
      <div>
      贵阳国家大数据安全靶场 - <span style=" font-weight: bold;">评测服务平台</span>
      <div class="admin_icon">{{username}}</div>
      <i class="line_icon"></i>
      </div>
      </div>
    </div>
    <div class="apply_list">
      <div class="cover_img">
        <i class="cover_icon"></i>
      </div>
      <div class="apply_btn">
        <div class="ruanjian"
             :style="tabIndex ===0?'transform: scale(1.1)':''"
             @mouseenter='tabIndex = 0'
             @click="toJump('软件测试',i.seq)"
             @mouseleave="tabIndex = ''"
             v-for="i in dataList"
             v-if="i.name == '软件检测'"
        >
          {{i.name}}
        </div>
        <div class="yingjian" :style="tabIndex ===3?'transform: scale(1.1)':''"
             v-for="i in dataList"
             v-if="i.name == '硬件检测'"
             @click="toJump('硬件检测',i.seq)"
             @mouseenter='tabIndex = 3' @mouseleave="tabIndex = ''">
          {{i.name}}
        </div>
        <div class="xinpian"
             :style="tabIndex ===4?'transform: scale(1.1)':''"
             v-for="i in dataList"
             v-if="i.name == '芯片检测'"
             @click="toJump('芯片检测',i.seq)"
             @mouseenter='tabIndex = 4' @mouseleave="tabIndex = ''">
          {{i.name}}
        </div>
        <div class="loudong"
             :style="tabIndex ===5?'transform: scale(1.1)':''"
             v-for="i in dataList"
             @click="toJump('漏洞检测',i.seq)"
             v-if="i.name == '漏洞检测'"
             @mouseenter='tabIndex = 5' @mouseleave="tabIndex = ''">
          {{i.name}}
        </div>
        <div class="xinjishu"
             :style="tabIndex ===2?'transform: scale(1.1)':''"
             @mouseenter='tabIndex = 2'
             @mouseleave="tabIndex = ''"
             @click="toJump('新技术检测',i.seq)"
             v-for="i in dataList"
             v-if="i.name == '新技术检测'"
        >
          {{i.name}}
        </div>
        <div class="app"
             :style="tabIndex ===1?'transform: scale(1.1)':''"
             @click="toJump('App',i.seq)"
             @mouseenter='tabIndex = 1'
             @mouseleave="tabIndex = ''"
             v-for="i in dataList"
             v-if="i.name == 'APP检测'"
        >
          {{i.name}}
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    data () {
      return {
        tabIndex:0,
        dataList:[],
        username:''
      }
    },
    methods:{
      toMainPage () {
        // window.location.href = '/test/platform_center.html'
      },
      // 获得菜单列表
      getMeunList () {
        let  moduleList = []
        this.$req().get(this.Api().getMeunList, function(res) {
          if(res.data.code == 200){
            for(let i = 0;i<res.data.data.acls.length;i++){
              if(res.data.data.acls[i].id == 17){
                 moduleList = res.data.data.acls[i].aclModuleList
              }
            }
            for(let j=0;j<moduleList.length;j++){
             if(moduleList[j].name == '申请端'){
               this.dataList = moduleList[j].aclModuleList
               for (let k=0;k<this.dataList.length;k++) {
                 if(this.dataList[k].name == '软件检测'){
                   this.dataList[k].img = require('../../assets/img/ruanjianjiance.png')
                 }
                 if(this.dataList[k].name == '硬件检测'){
                   this.dataList[k].img = require('../../assets/img/yingjianjiance.png')
                 }
                 if(this.dataList[k].name == '芯片检测'){
                   this.dataList[k].img = require('../../assets/img/xinpianjiance.png')
                 }
                 if(this.dataList[k].name == '漏洞检测'){
                   this.dataList[k].img = require('../../assets/img/loudongjiance.png')
                 }
                 if(this.dataList[k].name == '新技术检测'){
                   this.dataList[k].img = require('../../assets/img/xinjishujiance.png')
                 }
                 if(this.dataList[k].name == 'APP检测'){
                   this.dataList[k].img = require('../../assets/img/jianceshenhe.png')
                 }
               }
             }
            }
          }else {
            this.$Message.error(res.data.msg)
          }
        })
      },
      toJump (name,i) {
        localStorage.aclModuleList = JSON.stringify(this.dataList)
        this.$router.push('/detection/applyHomePage/tabTestApply?name=' + name +'&type='+ i)
      },
      getUserInfo () {
        this.$req().get(this.Api().getUserInfo, function(res) {
          this.username = res.data.username
        })
      }
    },
    mounted() {
      this.getUserInfo()
      this.getMeunList()
      this.username = localStorage.username
    }
  }
</script>
<style lang="less" scoped>
  .apply_title{
    position: relative;
    height:60px;
  }
  .page_back {
    width: 100%;
    height: 100vh;
    overflow-x: hidden;
    display: flex;
    flex-direction: column;
    background: url("../../assets/img/login/homePage.png") no-repeat;
    background-size: 100% 100%;
    position: relative;
    .page_title {
      position: absolute;
      top:0;
      left:0;
      z-index: 999999999999999999;
      display: flex;
      flex-direction: row;
      width:100%;
      div:first-child {
        width:174px;
        height:75px;
        background:url("../../assets/img/login/return_back.png") no-repeat;
        background-size: cover;
      }
      div:last-child {
        flex: 1;
        height:65px;
        width:100%;
        display: flex;
        align-items: flex-end;
        font-size: 30px;
        font-style: italic;
        margin-left: 10px;
        color: #69e3fb;
        position: relative;
        .line_icon {
          width: 100%;
          height: 11px;
          background: url("../../assets/img/liti/page_line.png") no-repeat;
          background-size: contain;
          position: absolute;
          bottom: -3px;
          left: -20px;
        }
        .admin_icon{
          width:188px;
          height:50px;
          line-height: 50px;
          text-align: center;
          background: url("../../assets/img/admin_btn.png") no-repeat;
          background-size: contain;
          position: absolute;
          bottom:0;
          right:20px;
        }
      }
    }

    .apply_list{
      flex: 1;
      display: flex;
      flex-direction: row;
      align-items: center;
      .cover_img{
        position: relative;
        min-width:45%;
        height:100%;
        border-radius: 50%;
        .cover_icon{
          width:125%;
          height:100%;
          background: url("../../assets/img/liti/zhegai.png") no-repeat;
          background-size: 100% 100%;
          position: absolute;
          left:0px;
          /*top:-7px;*/
          z-index: 999999;
        }
      }
      .apply_btn{
        div{
          width:100%;
          height:105px;
          font-size: 24px;
          text-align: center;
          color: #ffffff;
          cursor: pointer;
          position: relative;
          margin-bottom: 10px;
          padding-left: 60px;
          &:last-child{
            margin-bottom: 0px;
          }
        }
        .app{
          width:543px;
          height:105px;
          line-height: 105px;
          background: url("../../assets/img/liti/app.png") no-repeat;
          background-size: cover;
        }
        .loudong{
          width:543px;
          height:105px;
          line-height: 105px;
          background: url("../../assets/img/liti/loudong.png") no-repeat;
          background-size: cover;
        }
        .ruanjian{
          width:543px;
          height:105px;
          line-height: 105px;
          background: url("../../assets/img/liti/ruanjian.png") no-repeat;
          background-size: cover;
        }
        .xinjishu{
          width:543px;
          height:105px;
          line-height: 105px;
          background: url("../../assets/img/liti/xinjishu.png") no-repeat;
          background-size: cover;
        }
        .yingjian{
          width:543px;
          height:105px;
          line-height: 105px;
          background: url("../../assets/img/liti/yingjian.png") no-repeat;
          background-size: cover;
        }
        .xinpian{
          width:543px;
          height:105px;
          line-height: 105px;
          background: url("../../assets/img/liti/xinpian.png") no-repeat;
          background-size: cover;
        }
      }
    }
  }
</style>



