<template>
  <div class="commom_style">
    <firstTitle :name="$route.query.name"></firstTitle>
    <div class="input_box">
      <div class="search_box">
        <div class="commom_input">
          <label>工单编号:</label>
          <Input placeholder="请输入工单编号" style="margin-left: 10px;width:180px" v-model="postData.number"/>
        </div>
        <div class="commom_input">
          <label>工单状态:</label>
          <Select placeholder="请输入工单状态" style="margin-left: 10px;width:180px" v-model="postData.statusName" >
            <Option v-for="item in orderStatusList" :value="item.value" :key="item.value">{{ item.text }}</Option>
          </Select>
        </div>
        <div class="commom_input">
          <label>修改时间:</label>
          <DatePicker type="datetime" placeholder="起始时间"
                      style="width: 180px;margin-left: 10px;"
                      v-model="postData.updateTimeStart"
                      @on-change="selectStart"></DatePicker>
          &nbsp; —— &nbsp;
          <DatePicker type="datetime"
                      placeholder="结束时间"
                      style="width: 180px"
                      @on-change="selectEnd"
                      v-model="postData.updateTimeEnd"></DatePicker>
        </div>
      </div>
      <div class="search_btn">
        <Button type="primary" style="margin-left: 20px" @click="findOrderByPage()">搜索</Button>
        <Button type="primary" style="margin-left: 20px" @click="resetSearch()">重置</Button>
      </div>
    </div>
    <div style="display: flex;flex-direction: row-reverse">
      <div class="is_reading"
           @click="$router.push({path:'/detection/applyHomePage/newWork',query:{name:$route.query.name,type:$route.query.type}})">
        <i class="new_icon"></i>新建工单
      </div>
    </div>
    <Table stripe :columns="columns1" :data="data1">
      <template slot-scope="{ row, index }" slot="action">
        <span class="table_btn" v-if="row.status == 1"
              @click="$router.push({path:'/detection/applyHomePage/newWork',
              query:{name:$route.query.name,type:$route.query.type,id:row.id}})">修改</span>
        <span class="table_btn" @click="deleteOrder(row.id)" v-if="row.status == 1">删除</span>
        <span class="table_btn" @click="sumbitOrder(row.id)" v-if="row.status == 1">提交</span>
        <span class="table_btn grey_btn" v-if="row.status != 1">修改</span>
        <span class="table_btn grey_btn" v-if="row.status != 1">删除</span>
        <span class="table_btn grey_btn" v-if="row.status != 1">提交</span>
        <span class="table_btn" @click="toConnect(row.id)" v-if="row.status == 6 || row.status > 6">沟通留言</span>
        <span class="table_btn grey_btn" v-if="row.status < 6">沟通留言</span>
        <span class="table_btn grey_btn"  v-if="row.status != 9">报告查看</span>
        <span class="table_btn grey_btn"  v-if="row.status != 9">报告下载</span>
        <span class="table_btn" @click="jumpNext(row.reportUrl)" v-if="row.status == 9">报告查看</span>
        <span class="table_btn" @click="reportDowm(row.id,row.reportName)" v-if="row.status == 9">报告下载</span>
        <span class="table_btn" v-if="row.status == 2 || row.status > 2" @click="orderLook(row.id)">工单流转查看</span>
        <span class="table_btn" v-if="row.status == 2 || row.status > 2" @click="orderDown(row.id)">工单流转下载</span>
        <span class="table_btn grey_btn" v-if="row.status < 2">工单流转查看</span>
        <span class="table_btn grey_btn" v-if="row.status < 2">工单流转下载</span>
      </template>
    </Table>
    <splitPage :total="total" :page="postData.page"
               @psizeChange="psizeCha" @pageChange="pageCha"
               :rows="postData.rows"></splitPage>
    <commomComfirm ref="myComfirm" titleName="沟通留言" modalWidth="720">
      <div style="height:410px; overflow: auto; border: 1px solid #D8D8D8;padding:0 20px 20px;">
        <div class="message_box" v-for=" (item,index) in connectList">
          <div style="margin: 20px 0;font-size: 18px;">{{item.requestName}}:</div>
          <div class="img_file" v-if="item.imgUrl">
            <span>图片附件:</span>
            <span>
            <img :src="item.imgUrl" alt="">
          </span>
          </div>
          <div class="ques_detail">
            <span>问题描述:</span>
            <span>
            {{item.chat}}
          </span>
          </div>
          <span class="ask_time">提问时间：{{item.createTime}}</span>
        </div>
      </div>
      <div class="upload_file">
        <span>图片附件:</span>
        <span> <Upload action="" style="display: inline-block;" accept=".png,.jpeg" :before-upload="handleImgUpload" >
               <i class="upload_icon" v-if="!this.imgFile.fileName"></i>
                <b v-if="this.imgFile.fileName">{{this.imgFile.fileName}}</b>
              </Upload></span>
      </div>
      <div class="return_detail">
        <span>恢复描述：</span>
        <span>
          <Input type="textarea" placeholder="请输入描述" v-model="chat"/>
        </span>
      </div>
      <div class="form_btn">
        <Button type="primary" style="background: #687D97;" class="back_btn" @click="$refs.myComfirm.modal = false">取消</Button>
        <Button type="primary" class="back_btn" @click="saveConnectsContent()">保存</Button>
      </div>
    </commomComfirm>
    <commomComfirm ref="myOrderComfirm" titleName="工单流转" modalWidth="1200">
      <div class="orderComfirm">
        <p>工单基本信息</p>
        <div class="orderInfo">
          <div><span>工单编号</span><span>{{orderObj.order.number}}</span></div>
          <div><span>检测体附件</span><span>{{orderObj.order.checkBodyName}}</span></div>
          <div><span>文档附件</span><span>{{orderObj.order.wordName}}</span></div>
          <div><span>申请日期</span><span>{{orderObj.order.updateTime}}</span></div>
        </div>
        <p>威胁扫描结果</p>
        <ul v-if="!malWareReports.length"> 暂无威胁扫描结果 </ul>
        <ul>
          <li v-for="(item,index) in malWareReports">
            <div>
              {{item.ruleChname}}
            </div>
            <div v-if="!item.checkRetList.length" style="margin-left: 10px;color: #999999"> 检测完毕，没有发现恶意代码</div>
            <div v-if="item.checkRetList.length" style="margin-left: 10px;">
              <div v-for="(i, j) in item.checkRetList" class="check_detail">
                <span v-for="(o,p) in i" style="display: block;">{{p}}:{{o}}</span>
              </div>
            </div>
          </li>
        </ul>
        <p v-if="testList.length">审核过程详情</p>
        <div class="process_detaile">
          <div class="detail_list" v-for="(item,index) in testList">
            <div>
              <div>{{item.createTime}}</div>
              <div style="margin-left: 20px;display: flex;flex-direction: column">
                <span style="font-size: 14px;">{{item.statusName}}</span>
                <span style="font-size: 12px;color: #687D97;" v-if="item.noPassReason">驳回理由：</span>{{item.noPassReason}}</div>
            </div>
            <div>
              <span>操做人:</span>
              <span>{{item.operater}} <span style="color: #979797;" v-if="item.roleName">（{{item.roleName}}）</span></span>
            </div>
          </div>
        </div>
        <p v-if="checkList.length">检测过程详情</p>
        <div class="process_detaile">
          <div class="detail_list" v-for="(item,index) in checkList">
            <div>
              <div>{{item.createTime}}</div>
              <div style="margin-left: 20px;display: flex;flex-direction: column">
                <span style="font-size: 14px;">{{item.statusName}}</span>
                <span style="font-size: 12px;color: #687D97;" v-if="item.noPassReason">驳回理由：</span>{{item.noPassReason}}</div>
            </div>
            <div>
              <span>操做人:</span>
              <span>{{item.operater}} <span style="color: #979797;" v-if="item.roleName">（{{item.roleName}}）</span></span>
            </div>
          </div>
        </div>
        <div style="text-align: center;">
          <Button type="primary" style="width: 200px;margin:20px auto" @click="$refs.myOrderComfirm.modal = false">关闭</Button>
        </div>
      </div>

    </commomComfirm>
    <a :href="JumpUrl" ref="jumpA" style="display: none" target='_blank'></a>
  </div>
</template>
<script>
  import firstTitle from '../../components/firstTitle'
  import splitPage from '../../components/splitPage'
  import commomComfirm from '../../components/comomComfirm'
  export default {
    components:{firstTitle,splitPage,commomComfirm},
    mixins: [require('../../assets/js/common').commomMethods],
    data () {
      return {
        JumpUrl:'',
        columns1: [
          {
            title: '序号',
            type: 'index',
            align:'center',
            width:70,
          },
          {
            title: '工单编号',
            key: 'number',
            align:'center',
            tooltip: true,
          },
          {
            title: '工单类型',
            key: 'typeName',
            align:'center',
            tooltip: true
          },
          {
            title: '工单状态',
            key: 'statusName',
            align:'center',
            tooltip: true
          },
          {
            title: '修改日期',
            key: 'updateTime',
            align:'center',
            tooltip: true
          },
          {
            title: '操作',
            slot: 'action',
            align:'center',
          }
        ],
        data1: [],
        total:'',
        imgFile:{
          file:'',
          fileName:''
        },
        postData:{
          page:1,
          rows:10,
          type:'',
          number:'',
          updateTimeStart:'',
          updateTimeEnd:'',
        },
        orderStatusList:[],
        connectList:[],
        chat:'',
        orderId:'',
        orderObj:{
          order:{
            number:'',
            checkBodyName:'',
            wordName:'',
            updateTime:''
          }
        },
        testList:[],
        checkList:[],
        malWareReports:[]
      }
    },
    watch:{
      '$route.query.type' (n){
        this.postData ={
            page:1,
            rows:10,
            type:n,
            number:'',
            updateTimeStart:'',
            updateTimeEnd:'',
        }
        this.findOrderByPage()
        this.getOrderStatusList()
      }
    },
    methods:{
      lookTaskMoves (orderId) {
        this.$req().get(this.Api().lookTaskMoves+'?orderId='+ orderId, function(res) {
          if(res.data.code == 200){
            this.$refs.myOrderComfirm.modal = true
            this.orderObj = res.data.data
            let orderMoves = res.data.data.orderMoves
            this.malWareReports = res.data.data.malWareReports
            for (let i = 0; i<orderMoves.length; i++){
              if(orderMoves[i].type == 1){
                this.checkList.push(orderMoves[i])
              }else {
                this.testList.push(orderMoves[i])
              }
            }
          }else {
            this.$Message.error(res.data.msg)
          }
        })
      },
      // 工单流转查看
      orderLook (id) {
        this.lookTaskMoves(id)
      },
      orderDown (orderId) {
        this.$req().get(this.Api().downloadLoopOrder+'?id='+ orderId, function(res) {
          if (res.data) {
            let blob = new Blob([res.data], {})
            let fileName = '工单流转下载.docx'
            if(window.navigator.msSaveOrOpenBlob) {
              navigator.msSaveBlob(blob, fileName)
            } else {
              var link = document.createElement('a')
              link.href = window.URL.createObjectURL(blob)
              link.download = fileName
              //此写法兼容可火狐浏览器
              document.body.appendChild(link);
              link.style.display='none';
              link.click()
              //此写法兼容可火狐浏览器
              document.body.removeChild(link)
              //释放内存
              window.URL.revokeObjectURL(link.href)
            }
          }
        })
      },
      reportDowm (id,fileName) {
        this.commomDwon(id,fileName,'reportName')
      },
      // 查看报告
      jumpNext (url) {
        if(url){
          this.JumpUrl = url
          let _this = this
          setTimeout(function () {
            _this.$refs.jumpA.click()
          }, 500)
        }else {
          this.$Message.info('暂无报告')
        }
      },
      //工单状态
      getOrderStatusList(){
        this.$req().get(this.Api().getOrderStatusList+'?parentId='+ 660, function(res) {
          if(res.data.code == 200){
            this.orderStatusList = res.data.data
          }else {
            this.$Message.error(res.data.msg)
          }
        })
      },
      sumbitOrder (id) {
        this.$Modal.confirm({
          title: '是否提交工单',
          onOk: () => {
            this.$req().get(this.Api().sumbitOrder +
            '?id=' + id,function(res) {
                if(res.data.code == 200) {
                  this.$Message.info(res.data.msg);
                   this.findOrderByPage()
                } else {
                  this.$Message.error(res.data.msg);
                }
              })
          }
        });
      },
      deleteOrder (id) {
        this.$Modal.confirm({
          title: '是否删除工单',
          onOk: () => {
            this.$req().get(this.Api().deleteOrder +
              '?id=' + id,function(res) {
              if(res.data.code == 200) {
                    this.findOrderByPage()
              } else {
                this.$Message.error(res.data.msg);
              }
            })
          }
        });
      },
      selectEnd (val){
        this.postData.updateTimeEnd = val
      },
      selectStart (val){
        this.postData.updateTimeStart = val
      },
      psizeCha(val) {
        this.postData.rows = val
        this.findOrderByPage()
      },
      pageCha(val) {
        this.postData.page = val
        this.findOrderByPage()
      },
      resetSearch () {
        this.postData.number = ''
      },
      // 列表
      findOrderByPage () {
        this.$req().post(this.Api().findOrderByPage,this.postData, function(res) {
          if(res.data.code == 200) {
            this.data1 = res.data.data.list
            this.total = res.data.data.total
          }else {
            this.$Message.error(res.data.msg)
          }
        })
      },
      // 沟通留言
      toConnect (orderId) {
        this.imgFile = {
          file: '',
          fileName: ''
        }
        this.$refs.myComfirm.modal = true
        this.orderId = orderId
        this.getConnectsList(orderId)
      },
      // 沟通列表
      getConnectsList (orderId) {
        this.$req().get(this.Api().getConnectsList+'?orderId='+ orderId, function(res) {
          if(res.data.code == 200){
            this.connectList = res.data.data
          }else {
            this.$Message.error(res.data.msg)
          }
        })
      },
      // 上传沟通
      saveConnectsContent () {
        if (!this.chat.replace(/[ ]/g,"")){
          this.$Message.info('请输入描述')
          return false
        }
        var param = new FormData()
        param.append('orderId', this.orderId)
        param.append('chat', this.chat)
        param.append('files',  this.imgFile.file)
        this.$axios.post(this.Api().saveConnectsContent, param,{headers:{Authorization:'Bearer '+localStorage.getItem('token')}}).then(function(res) {
          if(res.data.code == 200) {
            this.chat = ''
            this.getConnectsList(this.orderId)
            this.$Message.info(res.data.msg)
          } else {
            this.$Message.error(res.data.msg)
          }
        }.bind(this)).catch(function(err) {
          console.log(err)
        })
      },
      handleImgUpload(file) {
        this.imgFile.file = file;
        this.imgFile.fileName = file.name
        return false
      },
    },
    mounted () {
      this.postData.type = this.$route.query.type
      if(this.postData.type){
        this.findOrderByPage()
      }
    }
  }
</script>
<style lang="less" scoped>
  @import "../../assets/css/commom";
  .form_btn{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width:500px;
    margin:0px auto;
    text-align: center;
  }
  .is_reading{
    width:85px;
    position: relative;
    cursor: pointer;
    text-align: right;
    .new_icon{
      width:14px;
      height:14px;
      background: url("../../assets/img/xinjian.png") no-repeat;
      background-size: contain;
      position: absolute;
      left: 0px;
      top:50%;
      transform: translate(0,-50%);
    }
    margin-bottom: 20px;
    color: #157AF7;
  }
  .input_box{
    .comborder;
    display: flex;
    flex-direction: row;
    margin-bottom: 20px;
    padding:10px 0;
    .search_box{
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      padding-top:10px;
    }
    .search_btn{
      min-width:180px;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      border-left: 1px solid #D8D8D8;
      padding:0 20px 0 0px;
      margin-left: 20px;
    }
  }
  .message_box{
    width:100%;
    border-top: 1px solid #D8D8D8;
    position: relative;
    &:first-child{
      border-top: none !important;
    }
    .ask_time{
      position: absolute;
      right:20px;
      bottom:-10px;
      font-size: 14px;
      color: #999999;
    }
    .img_file{
      display: flex;
      flex-direction: row;
      span:first-child{
        min-width: 70px;
        font-size: 14px;
        color: #333333;
      }
      span:last-child{
        width:100%;
        height:240px;
        background: #F3F4F5;
        display: flex;
        align-items: center;
        justify-content: center;
      }
      img{
        width:275px;
        max-height: 240px;
        height:auto;
      }
    }
    .ques_detail{
      display: flex;
      flex-direction: row;
      margin:20px 0;
      span:first-child{
        min-width: 70px;
        font-size: 14px;
        color: #333333;
      }
      span:last-child{
        font-size: 14px;
        color: #687D97;
      }
    }
  }
  .upload_file{
    display: flex;
    flex-direction: row;
    align-items: center;
    margin:20px 0;
    span:first-child{
      min-width: 70px;
      font-size: 14px;
      color: #333333;
    }
    span:last-child{
      height:40px;
      line-height: 40px;
      width:100%;
      position: relative;
      .upload_icon{
        width:150px;
        height:40px;
        background: url("../../assets/img/shangchuananniu.png") no-repeat;
        background-size: contain;
        position: absolute;
        top:0;
        left:0px;
      }
      b{
        font-weight: normal;
        color: red;
      }
    }
  }
  .return_detail{
    display: flex;
    flex-direction: row;
    margin:20px 0;
    span:first-child{
      min-width: 70px;
      font-size: 14px;
      color: #333333;
    }
    span:last-child{
      display: inline-block;
      width:100%;
    }
  }
  /deep/ textarea.ivu-input{
    height:110px;
  }
  .orderComfirm{
    border: 1px solid #D8D8D8;
    padding:0 20px;
    height:560px;
    overflow-y: auto;
    p{
      font-size: 18px;
      color: #333333;
      margin: 20px 0;
    }
    .orderInfo{
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      div{
        width:50%;
        margin-bottom: 10px;
        span:first-child{
          font-size: 18px;
          color: #ADADAD;
        }
        span:last-child{
          margin-left: 10px;
        }
      }
    }
    ul{
      li{
        margin:0 auto;
        width:100%;
        padding:10px 0;
        border-bottom: 1px solid #D8D8D8;
        display: flex;
        flex-direction: row;
        font-size: 14px;
        color: #333333;
        div:first-child{
          display: flex;
          min-width: 120px;
          width:120px;
        }
        .check_detail{
          display: flex;
          flex-direction: column;
        }
      }
    }
  }
  .process_detaile{
    font-size: 14px;
    .detail_list{
      position: relative;
      &:before{
        width: 5px;
        height:5px;
        border-radius: 50%;
        content: '';
        background: #157AF7;
        position: absolute;
        top: 7px;
        left: -3px;
      }
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      padding-left: 20px;
      margin-left: 5px;
      div:first-child{
        font-size: 14px;
        color: #333333;
        display: flex;
        flex-direction: row;
      }
      div:last-child{
        font-size: 14px;
      }
    }
  }
</style>



