<template>
  <div class="commom_style">
    <SecendTitle :name1="$route.query.role == 'admin'?'消息管理':'消息通知'" name2="详情"></SecendTitle>
    <div class="message_box">
       <div>
         <span>消息标题：</span><span style="font-size: 18px;">{{postData.title}}</span>
       </div>
      <div>
        <span>消息类型：</span><span style="font-size: 14px;">{{postData.type==1?'个人消息':'全员公告'}}</span>
      </div>
      <div v-if="postData.type==1">
        <span>执行人姓名：</span><span style="font-size: 14px;">{{postData.receivedName}}</span>
      </div>
      <div>
        <span>消息内容：</span><span class="info_message">{{postData.content}}</span>
      </div>
      <div class="commom_back_btn" @click="$router.go(-1)">返回</div>
    </div>
  </div>
</template>
<script>
  import SecendTitle from "../../components/secendTitle";
  export default {
    components: {SecendTitle},
    data () {
      return {
        postData:{
        title: '',
        content:'',
        type:'',
          receivedName:'',
      }
      }
    },
    methods:{
      messageDetailById (id) {
        this.$req().get(this.Api().messageDetailById + '?id='+id + '&status ='+ this.$route.query.status, function(res) {
          if(res.data.code == 200){
            this.postData={
              title: res.data.data.title,
              content:res.data.data.content,
              type:res.data.data.type,
              receivedName:res.data.data.receivedName
            }
          }else {
            this.$Message.error(res.data.msg)
          }
        })
      }
    },
    mounted () {
        this.messageDetailById(this.$route.query.id)
    }
  }
</script>
<style lang="less" scoped>
  @import "../../assets/css/commom";
  .message_box{
    .comborder;
    width:100%;
    padding:40px 60px 10px;
    div{
      display: flex;
      flex-direction: row;
      margin-bottom: 30px;
      span:first-child{
        min-width: 80px;
        font-size: 14px;
        color: #ADADAD;
      }
      span:last-child{
        color: #333333;
      }
      .info_message{
        width:100%;
        background: #F8F9FB;
        height:447px;
        padding: 30px;
        font-size: 14px;
      }
    }
  }
</style>



