<template>
  <div class="section">
    <v-header :url="jumpUrl"></v-header>
    <div class="section_box">
      <div class="left_aside">
         <div @click="totab(j,i.request)"
              v-for="(i,j) in dataList"
              :class="tabIndex==j?'select_meun':''">
           <Icon :type="i.icon" size="20"/>
           <span>{{i.name}}</span>
         </div>
      </div>
      <!--右侧子页面-->
      <div class="right-aside">
        <router-view/>
      </div>
    </div>
  </div>
</template>
<script>
  import vHeader from '../../components/header'
  export default {
    components:{
      vHeader
    },
    data () {
      return {
        tabIndex:0,
        dataList:[],
        jumpUrl: '/test/platform_center.html'
      }
    },
    methods:{
      totab(index,request){
        this.tabIndex = index
        this.$router.push(request)
      },
      // 获得菜单列表
      getMeunList () {
        let  moduleList = []
        this.$req().get(this.Api().getMeunList, function(res) {
          if(res.data.code == 200){
            for(let i = 0;i<res.data.data.acls.length;i++){
              if(res.data.data.acls[i].id == 17){
                moduleList = res.data.data.acls[i].aclModuleList
              }
            }
            for(let j=0;j<moduleList.length;j++){
              if(moduleList[j].name == '执行端'){
                this.dataList = moduleList[j].aclModuleList
              }
            }
            for(let k =0; k<this.dataList.length;k++){
              if(this.dataList[k].request == this.$route.path){
                this.tabIndex = k
              }
            }
          }else {
            this.$Message.error(res.data.msg)
          }
        })
      },
    },
    mounted () {
      this.getMeunList()
    }
  }
</script>
<style lang="less" scoped>
  .select_meun{
    background: #157AF7;
  }
  .section{
    display: flex;
    flex-direction: column;
    height: 100vh;
    overflow: hidden;
      .section_box{
        display: flex;
        flex-direction: row;
        height:90.5vh;
        flex: 1;
        .left_aside{
          width:320px;
          background: #112435;
          height:100%;
          padding-top:20px;
          div{
            height:50px;
            line-height: 50px;
            color: #ffffff;
            padding-left: 92px;
            display: flex;
            align-items: center;
            cursor: pointer;
            span{
              margin-left: 23px;
            }
          }
        }
        .right-aside{
          background: #ffffff;
          flex: 1;
          height:100%;
          overflow-y: auto;
        }
      }
  }

</style>



