<template>
  <div class="section">
    <v-header :url="jumpUrl" isRouter=true></v-header>
    <div class="section_box">
      <div class="left_aside">
        <div v-for="(i,j) in dataList" @click="totab(i.name,i.request,i.seq)" :class="tabIndex==i.seq?'select_meun':''">
          <Icon :type="i.icon" size="20"/>
          <span>{{i.name}}</span>
        </div>
      </div>
      <!--右侧子页面-->
      <div class="right-aside">
        <router-view/>
      </div>
    </div>
  </div>
</template>
<script>
  import vHeader from '../../components/header'
  export default {
    components:{
      vHeader
    },
    data () {
      return {
        tabIndex:1,
        dataList:[],
        jumpUrl:'/detection/applyIndex'
      }
    },
    methods:{
      totab(name,pathName,type){
        this.tabIndex = type
        this.$router.push({
          path:pathName,
          query:{
          name:name,
          type:type,
        }
        })
      }
    },
    mounted () {
      if(localStorage.aclModuleList) {
        this.dataList = JSON.parse(localStorage.aclModuleList)[0].aclModuleList
      }
      this.totab(this.$route.query.name,'',this.$route.query.type)
    }
  }
</script>
<style lang="less" scoped>
  .select_meun{
    background: #157AF7;
  }
  .section{
    display: flex;
    flex-direction: column;
    height: 100vh;
    overflow: hidden;
    .section_box{
      display: flex;
      flex-direction: row;
      height:90.5vh;
      flex:1;
      .left_aside{
        width:320px;
        background: #112435;
        height:100%;
        padding-top:20px;
        div{
          height:50px;
          line-height: 50px;
          color: #ffffff;
          padding-left: 92px;
          display: flex;
          align-items: center;
          cursor: pointer;
          span{
            margin-left: 23px;
          }
        }
      }
      .right-aside{
        background: #ffffff;
        flex: 1;
        height:100%;
        overflow-y: auto;
      }
    }
  }

</style>



