<template>
    <div class="commom_style">
    <SecendTitle :name1="$route.query.role == 'admin'?'检测审核':'任务工单'" name2="工单详情"></SecendTitle>
     <div class="base_info">
       <div class="base_info_detail">
         <p>基本信息</p>
         <div><span>检测类型:</span>
           <span>{{postData.typeName}}</span>
         </div>
         <div><span>工单编号:</span><span>{{postData.number}}</span></div>
         <div v-for="i in opationArr"><span>{{i.key}}:</span><span>{{i.value}}</span></div>
         <div><span>工单申请时间:</span><span>{{postData.createTime}}</span></div>
         <div><span>工单当前状态:</span><span>{{postData.statusName}}</span></div>
         <div @click="downFile(postData.checkBodyName,'checkBodyName')"><span>检测体附件:</span>
           <span class="blue_style" :title="postData.checkBodyName">{{postData.checkBodyName}}</span></div>
         <div @click="downFile(postData.wordName, 'wordName')"><span>文档附件:</span>
           <span class="blue_style">{{postData.wordName}}</span></div>
         <div @click="downFile(postData.imgName, 'imgName')"><span>图片制作:</span>
           <span class="blue_style">{{postData.imgName}}</span></div>
         <div class="commom_back_btn" @click="$router.go(-1)">返回</div>
       </div>
       <div class="base_img" v-if="imgList.length">
          <ul>
            <li v-for="(item,index) in imgList" @click="toTabImg(index,item)">
              <img :src="item.url" alt="" :class="imgIndex == index?'isSelectImg':''">
             </li>
          </ul>
         <!--<div :style="'background: url('+ isSelectImgUrl+') no-repeat; filter: blur(15px);width:100%;height:100%;position:relative'">-->
          <div class="show_img">
           <div class="img_name">
             {{selectImgName}}
           </div>
              <div class="img_box" style="cursor: pointer">
                <i class="trans_icon" @click="transformImg()"></i>
                <i class="da_icon" @click="changeSize(0)"></i>
                <i class="xiao_icon" @click="changeSize(1)"></i>
                <div style="overflow: hidden;text-align: center;width: 100%;height:100%;">
                  <img :src="isSelectImgUrl" alt="" :style="'width:' +imgSize +'%; transform: rotate('+transformData+'deg);'">
                  <i class="left_btn" @click="next(0)"></i>
                  <i class="right_btn"  @click="next(1)"></i>
                </div>
              </div>
          </div>
         <!--</div>-->
       </div>
     </div>
  </div>
</template>
<script>
  import SecendTitle from "../../components/secendTitle";
  export default {
    components: {SecendTitle},
    mixins: [require('../../assets/js/common').commomMethods],
    data () {
      return {
        postData:{
          number:'',
        },
        opationArr:[],
        imgList:[],
        imgIndex:0,
        isSelectImgUrl:'',
        selectImgName:'',
        imgSize:100,
        transformData:0,
      }
    },
    methods:{
      toTabImg (index,item) {
        this.imgIndex = index
        this.isSelectImgUrl = item.url
        this.selectImgName = item.name
      },
      transformImg () {
        this.transformData =  this.transformData+90
        console.log( this.transformData, ' this.transformData')
      },
      changeSize (size) {
        if(size){
          this.imgSize --
        }else {
          this.imgSize ++
        }
      },
      next (dir) {
        if(dir){
          this.imgIndex = this.imgIndex + 1
          if( this.imgIndex == this.imgList.length){
            this.imgIndex=0
          }
          this.isSelectImgUrl = this.imgList[this.imgIndex].url
          this.selectImgName = this.imgList[this.imgIndex].name
        }else {
          if(this.imgIndex == 0){
            this.imgIndex=this.imgList.length
          }
          this.imgIndex = this.imgIndex - 1
          this.isSelectImgUrl = this.imgList[this.imgIndex].url
          this.selectImgName = this.imgList[this.imgIndex].name
        }
      },
      // 下载文件
      downFile (fileName,paName) {
        this.commomDwon(this.postData.id,fileName,paName)
      },
      infoFindOrderById () {
        this.$req().get(this.Api().infoFindOrderById+'?id='+this.$route.query.id, function(res) {
          if(res.data.code == 200){
            this.postData=res.data.data
            let obj = JSON.parse(this.postData.option)
            for (let i in obj){
              this.opationArr.push({
                   key:i,
                   value:obj[i]
              })
            }
            this.imgList = []
            if( this.isImg(this.postData.wordName)){
              this.imgList.push({
                name:this.postData.wordName,
                url:this.postData.wordUrl
              })
            }
            if( this.isImg(this.postData.checkBodyName)){
              this.imgList.push({
                name:this.postData.checkBodyName,
                url:this.postData.checkBodyUrl
              })
            }
            if( this.isImg(this.postData.imgName)){
              this.imgList.push({
                name:this.postData.imgName,
                url:this.postData.imgUrl
              })
            }
            this.isSelectImgUrl = this.imgList[0].url
            this.selectImgName = this.imgList[0].name

          }else {
            this.$Message.error(res.data.msg)
          }
        })
      },
      // 判断是否是图片
      isImg (imgUrl) {
            if(/\.(gif|jpg|jpeg|png|GIF|JPG|PNG)$/.test(imgUrl)) {
              return true;
       }
      }
    },
    mounted () {
        this.infoFindOrderById()
    }
  }
</script>
<style lang="less" scoped>
  @import "../../assets/css/commom";
  .isSelectImg{
    border:1px solid red ;
  }
.base_info{
  display: flex;
  flex-direction: row;
  .base_info_detail{
    flex: 1;
    .comborder;
    p{
      font-size: 18px;
      color: #333333;
      font-weight: bold;
      margin:20px 20px 40px;
    }
    div{
      cursor: pointer;
      margin: 20px 40px 0 80px;
      display: flex;
      flex-direction: row;
      span:first-child{
        min-width:120px;
        font-size: 18px;
        color: #ADADAD;
      }
      span:last-child{
        margin-left: 80px;
        font-size: 18px;
        color: #333333;
        display: inline-block;
        .wordsEllipsis(1)
      }
      .blue_style{
        color: #157AF7 !important;
      }
    }
  }
  .base_img{
    width:50%;
    margin-left: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    .show_img{
      .comborder;
      width:100%;
      height:100%;
      margin-top: 20px;
      .img_name{
        height: 54px;
        line-height: 54px;
        background: rgba(0,0,0,0.40);
        text-align: center;
        color: #ffffff;
        border-top-left-radius: 4px;
        border-top-right-radius: 4px;
      }
      .img_box{
        padding:40px;
        position: relative;
        .trans_icon{
          width:28px;
          height:28px;
          background: url("../../assets/img/fanzhuan.png") no-repeat;
          background-size: cover;
          position: absolute;
          left:0;
          top:5px;
        }
        .da_icon{
          width:26px;
          height:26px;
          background: url("../../assets/img/jia.png") no-repeat;
          background-size: cover;
          position: absolute;
          right:40px;
          top:5px;
        }
        .xiao_icon{
          width:26px;
          height:26px;
          background: url("../../assets/img/jian.png") no-repeat;
          background-size: cover;
          position: absolute;
          right:5px;
          top:5px;
        }
          div{
            position: relative;
            .left_btn{
              width:70px;
              height:70px;
              background: url("../../assets/img/zuo.png") no-repeat;
              background-size: cover;
              position: absolute;
              top:50%;
              transform: translate(0,-50%);
              left:0px;
              z-index: 999;
            }
            .right_btn{
              width:70px;
              height:70px;
              background: url("../../assets/img/right.png") no-repeat;
              background-size: cover;
              position: absolute;
              top:50%;
              transform: translate(0,-50%);
              right:0px;
              z-index: 999;
            }
            img {
              width: 100%;
              max-width: 692px;
              height: auto;
              max-height: 502px;
            }
        }
      }
    }
    ul{
      .comborder;
      display: flex;
      flex-direction: row;
      justify-content: center;
      padding:20px 30px;
      li{
        max-width:300px;
        max-height:200px;
        text-align: center;
        &:nth-child(1){
          marign-left:20px;
        }
      }
      img{
        width:95%;
        max-width:300px;
        max-height:200px;
      }
    }
  }
}
</style>



