<template>
  <div class="login_back">
    <div class="login_title">
      <div @click="$router.go(-1)"></div>
      <div>
        贵阳国家大数据安全靶场 - <span style=" font-weight: bold;">评测服务平台</span>
        <i class="line_icon"></i>
      </div>
    </div>
    <div class="login_box">
        <div style="font-size: 24px; color: #69e3fb;margin-bottom: 20px;">测试服务管理系统</div>
        <div style="color: #69e3fb;">登录</div>
         <div class="login-input">
           <i-form ref="loginRef" :model="postData" :rules="ruleValidate">
             <Form-item prop="username">
               <i-input type="text" v-model="postData.username"  maxlength="30" placeholder="请输入用户名" @on-enter="login">
               </i-input>
               <i class="userIcon" slot></i>
             </Form-item>
             <Form-item prop="password">
               <i-input type="password" v-model="postData.password" placeholder="请输入密码" @on-enter="login">
               </i-input>
               <i class="passwardIcon" slot></i>
             </Form-item>
             <div style="display: flex;flex-direction: row">
               <Form-item prop="imageCode">
                 <i-input type="text" placeholder="请输入验证码" v-model="postData.imageCode" @on-enter="login" style="width:220px;">
                 </i-input>
                 <i class="resCordIcon" slot></i>
               </Form-item>
               <div class="resCord_img" @click="getCode">
                 <img :src="resCodeImg" alt="" style="width:140px!important;height:40px !important;">
               </div>
             </div>
           </i-form>
         </div>
         <div class="login_btn" @click="login"></div>
      <span class="forget_pass" @click="$refs.myComfirm.modal = true">忘记密码？</span>
    </div>
    <loginComfirm ref="myComfirm" titleName="忘记密码" modalWidth="500">
        <div style="text-align: center; color: #69e3fb; background-color: #000000 !important;">
          <div style="margin:20px auto 30px; font-weight: bold">
            请联系管理员进行密码重置
          </div>
        </div>
    </loginComfirm>
  </div>
</template>
<script>
  import loginComfirm from '../../components/loginComfirm'
  import JSEncrypt from 'jsencrypt/bin/jsencrypt'
  export default {
    components:{loginComfirm},
    data () {
      return {
        ruleValidate: {
          username: [
            {required: true, message: '用户名不能为空', trigger: 'blur'}
          ],
          password: [
            {required: true, message: '密码不能为空', trigger: 'blur'},
          ],
          imageCode: [
            {required: true, message: '验证码不能为空', trigger: 'blur'},
          ],
        },
        resCodeImg:'',
        postData:{
          username:'',
          password:'',
          imageCode:'',
          deviceId:'',
          grant_type:'password'
        },
        publicKey:''
      }
    },
    methods:{
      // 获得验证码
      getCode () {
          this.postData.deviceId = (new Date()).getTime()
          this.resCodeImg =this.Api().getCode+'?deviceId=' + (new Date()).getTime()
      },
      login () {
        let encryptor = new JSEncrypt
        encryptor.setPublicKey(this.publicKey)
        let safePassward =  encryptor.encrypt(this.postData.password)
        this.newObj = Object.assign({}, this.postData);
        this.newObj.password = safePassward
        this.$refs.loginRef.validate((valid) => {
          if (valid) {
            this.$req().loginNoToken(this.Api().getToken,this.newObj, function(res) {
               if(res.data.code == 401){
                 this.$Message.error(res.data.msg)
                 this.getCode()
               }else {
                 localStorage.setItem('token', res.data.access_token)
                 this.$router.push('/detection/applyIndex')
               }
            })
          }
        })
      },
      getPublicKey () {
        this.$req().getNoToken(this.Api().getPublicKey, function(res) {
          if(res.data.code == 200){
            this.publicKey = res.data.data
          }else {
            this.$Message.error(res.data.msg)
          }
        })
      }
    },
    mounted() {
      this.getCode()
      this.getPublicKey()
    }
  }
</script>
<style lang="less" scoped>
  /deep/ .ivu-form-item-error-tip{
    padding-left: 50px !important;
  }
.login_back{
  width:100%;
  height:100vh;
  background: url("../../assets/img/login/loginBack.png") no-repeat;
  background-size: 100% 100%;
  position: relative;
  .login_title{
    display: flex;
    flex-direction: row;
    div:first-child{
      width:174px;
      height:75px;
      background:url("../../assets/img/login/return_back.png") no-repeat;
      background-size: cover;
    }
    div:last-child{
      flex: 1;
      height:65px;
      display: flex;
      align-items: flex-end;
      font-size: 30px;
      font-style: italic;
      color: #69e3fb;
      position: relative;
      margin-left: 10px;
      .line_icon{
        width:100%;
        height:11px;
        background: url("../../assets/img/login/line.png") no-repeat;
        background-size: contain;
        position: absolute;
        bottom: -3px;
        left:-20px;
      }
    }
  }
  .login_box{
    width:600px;
    height: 610px;
    background: url("../../assets/img/login/loginBox.png") no-repeat;
    background-size: cover;
    position: absolute;
    top:50%;
    left:50%;
    margin-top: 30px;
    transform: translate(-50%,-50%);
    z-index: 100;
    text-align: center;
    padding:60px 0;
    .login-input{
      width:380px;
      margin:60px auto 0;
      .resCord_img{
        width:200px;
        height:40px;
        cursor:pointer;
        background: #ffffff;
        margin-left: 10px;
        border-radius: 4px
      }
    }
    .login_btn{
      margin:50px auto 0;
      width:372px;
      height:62px;
      background: url("../../assets/img/login/button.png") no-repeat;
      background-size: cover;
      cursor: pointer;
    }
    .forget_pass{
      color: #ffffff;
      position: absolute;
      bottom:20px;
      right:20px;
      cursor: pointer;
    }
  }
}
/deep/.ivu-input{
  background: #000000 !important;
  padding-left: 50px;
  color: #ffffff;
  height: 40px;
  line-height: 40px;

}
.userIcon {
  width: 14px;
  height: 16px;
  background: url("../../assets/img/login/yonghuming.png") no-repeat;
  background-size: contain;
  position: absolute;
  top: 50%;
  left: 20px;
  transform: translate(0, -50%);
}
  .passwardIcon{
    width: 12px;
    height: 16px;
    background: url("../../assets/img/login/mima.png") no-repeat;
    background-size: contain;
    position: absolute;
    top: 50%;
    left: 20px;
    transform: translate(0, -50%);
  }
  .resCordIcon{
    width: 14px;
    height: 16px;
    background: url("../../assets/img/login/cord.png") no-repeat;
    background-size: contain;
    position: absolute;
    top: 50%;
    left: 20px;
    transform: translate(0, -50%);
  }
</style>




