<template>
  <div class="commom_style">
    <firstTitle :name="$route.query.number?'首页':'任务工单'"></firstTitle>
    <div class="input_box">
      <div class="search_box">
        <div class="commom_input">
          <label>工单编号:</label>
          <Input placeholder="请输入工单编号" style="margin-left: 10px;width:160px" v-model="postData.number" @on-enter="findCheckTask"/>
        </div>
        <div class="commom_input">
          <label>工单状态:</label>
          <Select placeholder="请选择工单状态" style="margin-left: 10px;width:160px" v-model="postData.status" @on-change="findCheckTask">
            <Option v-for="(i,j) in orderStatusList" :value="i.value">{{i.text}}</Option>
          </Select>
        </div>
        <div class="commom_input" style="margin-right: 20px;">
          <label>修改日期:</label>
          <DatePicker type="datetime" placeholder="起始时间"
                      style="width: 180px;margin-left: 10px;"
                      v-model="postData.updateTimeStart"
                      @on-change="selectStart"></DatePicker>
          &nbsp; — &nbsp;
          <DatePicker type="datetime"
                      placeholder="结束时间"
                      style="width: 180px"
                      @on-change="selectEnd"
                      v-model="postData.updateTimeEnd"></DatePicker>
        </div>
      </div>
      <div class="search_btn">
        <Button type="primary" style="margin-left: 20px" @click="findCheckTask()">搜索</Button>
        <Button type="primary" style="margin-left: 20px" @click="resetData()">重置</Button>
      </div>
    </div>
    <Table stripe :columns="columns1" :data="data1">
      <template slot-scope="{ row, index }" slot="action">
        <span class="table_btn" @click="$router.push('/detection/actuatingIndex/taskSheetDetail?id='+row.orderId +'&role=actuating')">工单详情</span>
        <span class="table_btn" @click="synchonismScheme(row.id)">方案同步</span>
        <span class="table_btn grey_btn"  v-if="row.status == 6">方案同步</span>
        <span class="table_btn" @click="createOrder(row.id)" v-if="row.status == 7">订单生成</span>
        <span class="table_btn" @click="toConnect(row.orderId)">沟通留言</span>

        <span class="table_btn grey_btn" v-if="row.status != 7">订单生成</span>
        <span class="table_btn" @click="openUploadComfirm(row.orderId,row.typeName)" v-if="row.status == 8">报告上传</span>
        <span class="table_btn" @click="finishTask(row.id)" v-if="row.status == 8">任务完结</span>
        <span class="table_btn grey_btn" v-if="row.status != 8">报告上传</span>
        <span class="table_btn grey_btn" v-if="row.status != 8">任务完结</span>
      </template>
    </Table>
    <splitPage :total="total" :page="postData.page"
               @psizeChange="psizeCha" @pageChange="pageCha"
               :rows="postData.rows"></splitPage>
    <commomComfirm ref="myUpload" titleName="报告上传" modalWidth="400">
      <div style="text-align: center;">
        <Upload action="" style="display: inline-block" accept=".pdf" :before-upload="handleReportUpload" >
          <Button type="primary" v-if="!this.reportFile.fileName"> 点击上传</Button>
          <b v-if="this.reportFile.fileName" style="color: red;">{{this.reportFile.fileName}}</b>
        </Upload>
        <div class="upload_btn">
          <Button type="primary" style="background: #687D97;width:100px"  @click="$refs.myUpload.modal = false">取消</Button>
          <Button type="primary" style="width:100px" @click="uploadFile()">保存</Button>
        </div>
      </div>
    </commomComfirm>
    <commomComfirm ref="myComfirm" titleName="沟通留言" modalWidth="720">
      <div style="height:410px; overflow: auto; border: 1px solid #D8D8D8;padding:0 20px 20px;">
        <div class="message_box" v-for=" (item,index) in connectList">
          <div style="margin: 20px 0;font-size: 18px;">{{item.requestName}}:</div>
          <div class="img_file" v-if="item.imgUrl">
            <span>图片附件:</span>
            <span>
            <img :src="item.imgUrl" alt="">
          </span>
          </div>
          <div class="ques_detail">
            <span>问题描述:</span>
            <span>
            {{item.chat}}
          </span>
          </div>
          <span class="ask_time">提问时间：{{item.createTime}}</span>
        </div>
      </div>
      <div class="upload_file">
        <span>图片附件:</span>
        <span> <Upload action="" style="display: inline-block;" accept=".png,.jpeg" :before-upload="handleImgUpload" >
               <i class="upload_icon" v-if="!this.imgFile.fileName"></i>
                <b v-if="this.imgFile.fileName">{{this.imgFile.fileName}}</b>
              </Upload></span>
      </div>
      <div class="return_detail">
        <span>恢复描述：</span>
        <span>
          <Input type="textarea" placeholder="请输入描述" v-model="chat"/>
        </span>
      </div>
      <div class="form_btn">
        <Button type="primary" style="background: #687D97;" class="back_btn" @click="$refs.myComfirm.modal = false">取消</Button>
        <Button type="primary" class="back_btn" @click="saveConnectsContent()">保存</Button>
      </div>
    </commomComfirm>
  </div>
</template>
<script>
  import firstTitle from '../../components/firstTitle'
  import splitPage from '../../components/splitPage'
  import commomComfirm from '../../components/comomComfirm'
  export default {
    components:{firstTitle,splitPage,commomComfirm},
    data () {
      return {
        reportFile:{
          fileName:'',
          file:''
        },
        imgFile:{
          fileName:'',
          file:''
        },
        columns1: [
          {
            title: '序号',
            type: 'index',
            align:'center',
            width:70,
          },
          {
            title: '工单编号',
            key: 'number',
            align:'center',
            tooltip: true,
          },
          {
            title: '工单类型',
            key: 'typeName',
            align:'center',
            tooltip: true
          },
          {
            title: '工单状态',
            key: 'statusName',
            align:'center',
            tooltip: true
          },
          {
            title: '修改日期',
            key: 'updateTime',
            align:'center',
            tooltip: true
          },
          {
            title: '操作',
            slot: 'action',
            align:'center',
            minWidth:80
          }
        ],
        data1: [],
        total:'',
        postData:{
          page:1,
          rows:10,
          number:'',
          status:'',
          updateTimeStart:'',
          updateTimeEnd:''
        },
        orderStatusList:[],
        uploadId: '',
        typeName:'',
        connectList:[],
        chat:'',
        orderId:'',
      }
    },
    watch:{
      '$route.query.number' (o){
        if(!o){
          this.postData.number = ''
          this.postData.status = ''
          this.postData.updateTimeStart = ''
          this.postData.updateTimeEnd = ''
          this.findCheckTask()
        }
      }
    },
    methods:{
      // 上传沟通
      saveConnectsContent () {
        var param = new FormData()
        param.append('orderId', this.orderId)
        param.append('chat', this.chat)
        param.append('files',  this.imgFile.file)
        this.$axios.post(this.Api().saveConnectsContent, param,{headers:{Authorization:'Bearer '+localStorage.getItem('token')}}).then(function(res) {
          if(res.data.code == 200) {
            this.getConnectsList(this.orderId)
            this.$Message.info(res.data.msg)
          } else {
            this.$Message.error(res.data.msg)
          }
        }.bind(this)).catch(function(err) {
          console.log(err)
        })
      },
      // 沟通留言
      toConnect (orderId) {
        this.$refs.myComfirm.modal = true
        this.orderId = orderId
        this.getConnectsList(orderId)
      },
      // 沟通列表
      getConnectsList (orderId) {
        this.$req().get(this.Api().getConnectsList+'?orderId='+ orderId, function(res) {
          if(res.data.code == 200){
             this.connectList = res.data.data
          }else {
            this.$Message.error(res.data.msg)
          }
        })
      },
      // 重置
      resetData () {
        this.postData.number = ''
        this.postData.status = ''
        this.postData.updateTimeStart = ''
        this.postData.updateTimeEnd = ''
      },
      findCheckTask () {
        this.$req().post(this.Api().findCheckTask,this.postData, function(res) {
          if(res.data.code == 200) {
            this.data1 = res.data.data.list
            this.total = res.data.data.total
          }else {
            this.$Message.error(res.data.msg)
          }
        })
      },
      psizeCha(val) {
        this.postData.rows = val
        this.findCheckTask()
      },
      pageCha(val) {
        this.postData.page = val
        this.findCheckTask()
      },
      selectEnd (val){
        this.postData.updateTimeEnd = val
      },
      selectStart (val){
        this.postData.updateTimeStart = val
      },
      //工单状态
      getOrderStatusList(){
        this.$req().get(this.Api().getOrderStatusList+'?parentId='+ 660, function(res) {
          if(res.data.code == 200){
            this.orderStatusList = res.data.data
          }else {
            this.$Message.error(res.data.msg)
          }
        })
      },
      // 方案同步
      synchonismScheme (id) {
        this.$Modal.confirm({
          title: '是否同步方案',
          onOk: () => {
            this.$req().get(this.Api().synchonismScheme+'?id='+ id, function(res) {
              if(res.data.code == 200){
                this.$Message.info(res.data.msg)
                this.findCheckTask()
              }else {
                this.$Message.error(res.data.msg)
              }
            })
          }
        })
      },
      // 生成订单
      createOrder (id) {
        this.$Modal.confirm({
          title: '是否生成订单',
          onOk: () => {
            this.$axios({
              method: 'get',
              url: this.Api().createOrder +'?id=' +id, // 请求地址
              responseType: 'blob' // 表明返回服务器返回的数据类型
            }).then(function(res){
              let blob = new Blob([res.data], {})
              let fileName = '订单.zip'
              if(window.navigator.msSaveOrOpenBlob) {
                navigator.msSaveBlob(blob, fileName)
              } else {
                var link = document.createElement('a')
                link.href = window.URL.createObjectURL(blob)
                link.download = fileName
                //此写法兼容可火狐浏览器
                document.body.appendChild(link);
                link.style.display='none';
                link.click()
                //此写法兼容可火狐浏览器
                document.body.removeChild(link)
                //释放内存
                window.URL.revokeObjectURL(link.href)
              }
            }.bind(this)).catch(function(err){
              console.log(err)
            })
          }
        })
      },
      // 完结任务
      finishTask (id) {
        this.$Modal.confirm({
          title: '是否确定任务完结',
          onOk: () => {
            this.$req().get(this.Api().finishTask+'?id='+ id, function(res) {
              if(res.data.code == 200){
                this.$Message.info(res.data.msg)
                this.findCheckTask()
              }else {
                this.$Message.error(res.data.msg)
              }
            })
          }
        })
      },
      handleReportUpload(file) {
        this.reportFile.file = file;
        this.reportFile.fileName = file.name
        return false
      },
      handleImgUpload(file) {
        this.imgFile.file = file;
        this.imgFile.fileName = file.name
        return false
      },
      // 打开上传弹框
      openUploadComfirm (id,typeName) {
        this.reportFile.file =''
        this.reportFile.fileName = ''
        this.$refs.myUpload.modal = true
        this.uploadId = id
        this.typeName = typeName
      },
      // 上传报告
      uploadFile () {
        if(!this.reportFile.file){
          this.$Message.info('请选择文件')
          return false
        }
        var param = new FormData()
        param.append('type', this.typeName)
        param.append('id', this.uploadId)
        param.append('files',  this.reportFile.file)
        param.append('reportName',  this.reportFile.fileName)
        this.$axios.post(this.Api().uploadFile, param,{headers:{Authorization:'Bearer '+localStorage.getItem('token')}}).then(function(res) {
          if(res.data.code == 200) {
            this.$Message.info(res.data.msg)
            this.$refs.myUpload.modal = false
          } else {
            this.$Message.error(res.data.msg)
          }
        }.bind(this)).catch(function(err) {
          console.log(err)
        })
      }
    },
    mounted () {
      this.getOrderStatusList()
      this.findCheckTask()
      if(this.$route.query.number) {
        this.postData.number = this.$route.query.number
        this.findCheckTask()
      }
    }
  }
</script>
<style lang="less" scoped>
  @import "../../assets/css/commom";
  .form_btn{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width:500px;
    margin:0 auto;
    text-align: center;
  }
  .upload_btn{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width:260px;
    margin:60px auto 20px;
    text-align: center;
  }
  .input_box{
    .comborder;
    display: flex;
    flex-direction: row;
    margin-bottom: 20px;
    padding:10px 0;
    .search_box{
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      padding-top:10px;
    }
    .search_btn{
      min-width:180px;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      border-left: 1px solid #D8D8D8;
      padding:0 20px 0 0px;
      margin-left: 20px;
    }
  }

  .message_box{
    width:100%;
    border-top: 1px solid #D8D8D8;
    position: relative;
    &:first-child{
      border-top: none !important;
    }
    .ask_time{
      position: absolute;
      right:20px;
      bottom:-10px;
      font-size: 14px;
      color: #999999;
    }
    .img_file{
      display: flex;
      flex-direction: row;
      span:first-child{
        min-width: 70px;
        font-size: 14px;
        color: #333333;
      }
      span:last-child{
        width:100%;
        height:240px;
        background: #F3F4F5;
        display: flex;
        align-items: center;
        justify-content: center;
      }
      img{
        width:275px;
        max-height: 240px;
        height:auto;
      }
    }
    .ques_detail{
      display: flex;
      flex-direction: row;
      margin:20px 0;
      span:first-child{
        min-width: 70px;
        font-size: 14px;
        color: #333333;
      }
      span:last-child{
        font-size: 14px;
        color: #687D97;
      }
    }
  }

  .upload_file{
    display: flex;
    flex-direction: row;
    align-items: center;
    margin:20px 0;
    span:first-child{
      min-width: 70px;
      font-size: 14px;
      color: #333333;
    }
    span:last-child{
      height:40px;
      line-height: 40px;
      width:100%;
      position: relative;
      .upload_icon{
        width:150px;
        height:40px;
        background: url("../../assets/img/shangchuananniu.png") no-repeat;
        background-size: contain;
        position: absolute;
        top:0;
        left:0px;
      }
      b{
        font-weight: normal;
        color: red;
      }
    }
  }
  .return_detail{
    display: flex;
    flex-direction: row;
    margin:20px 0 0;
    span:first-child{
      min-width: 70px;
      font-size: 14px;
      color: #333333;
    }
    span:last-child{
      display: inline-block;
      width:100%;
    }
  }
  /deep/ textarea.ivu-input{
    height:120px;
  }
</style>



