<template>
  <div class="commom_style">
    <SecendTitle name1="消息管理" :name2="$route.query.id?'编辑消息':'新建消息'"></SecendTitle>
    <div class="message_box">
      <Form ref="formValidate" :model="postData" :rules="ruleValidate" :label-width="100">
        <FormItem label="消息标题" prop="title" style="width:550px">
          <Input v-model="postData.title" placeholder="请输入消息标题"></Input>
        </FormItem>
        <FormItem label="消息类型" prop="type" style="width:550px">
          <Select v-model="postData.type" placeholder="请输入消息类型">
            <Option value="1">个人消息</Option>
            <Option value="2">全员公告</Option>
          </Select>
        </FormItem>
        <FormItem label="执行人姓名" prop="receivedName" style="width:550px" v-if="postData.type == 1">
          <Select v-model="postData.receivedName" placeholder="请选择执行人姓名">
            <Option v-for="(item,index) in userName" :value="item.username">{{item.username}}</Option>
          </Select>
        </FormItem>
        <FormItem label="消息内容" prop="content" style="width:550px;">
          <Input v-model="postData.content"
                 show-word-limit
                 maxlength="200"
                 type="textarea"
                 placeholder="请输入消息内容"></Input>
        </FormItem>
        <div class="form_btn">
          <Button type="primary" style="background: #687D97;" class="back_btn" @click="$router.go(-1)">取消</Button>
          <Button type="primary" class="back_btn" @click="handleSubmit('formValidate')">完成</Button>
        </div>
      </Form>
    </div>
  </div>
</template>
<script>
  import SecendTitle from "../../components/secendTitle";
  export default {
    components: {SecendTitle},
    data() {
      return {
        ruleValidate: {
          title: [
            {required: true, message: '请输入消息标题', trigger: 'blur'}
          ],
          receivedName:[
            {required: true, message: '请选择执行人姓名', trigger: 'change'}
          ],
          type: [
            {required: true, message: '请输入消息类型', trigger: 'change'}
          ],
          content: [
            {required: true, message: '请输入消息内容', trigger: 'blur'},
          ]
        },
        postData:{
          title:'',
          content:'',
          type:'',
        },
        userName:[]
      }
    },
    methods: {
      saveMessage () {
        this.$req().post(this.Api().saveMessage,this.postData, function(res) {
          if(res.data.code == 200) {
            this.$router.go(-1)
          }else {
            this.$Message.error(res.data.msg)
          }
        })
      },
      getActionName () {
        this.$req().get(this.Api().getActionName, function(res) {
          if(res.data.code == 200){
            this.userName = res.data.data
          }else {
            this.$Message.error(res.data.msg)
          }
        })
      },
      // 修改消息
      updateMessage () {
        this.$req().post(this.Api().updateMessage,this.postData, function(res) {
          if(res.data.code == 200) {
            this.$router.go(-1)
          }else {
            this.$Message.error(res.data.msg)
          }
        })
      },
      handleSubmit (name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            if(this.$route.query.id){
              this.updateMessage()
            }else {
              this.saveMessage()
            }
          }
        })
      },
      handleReset (name) {
        this.$refs[name].resetFields();
      },
      messageDetailById (id) {
        this.$req().get(this.Api().messageDetailById + '?id='+id, function(res) {
          if(res.data.code == 200){
            this.postData={
                id:res.data.data.id,
                title: res.data.data.title,
                content:res.data.data.content,
                type:res.data.data.type,
                receivedName: res.data.data.receivedName
            }
          }else {
            this.$Message.error(res.data.msg)
          }
        })
      }
    },
    created () {
      this.getActionName()
    },
    mounted () {
      if(this.$route.query.id){
        this.messageDetailById(this.$route.query.id)
      }
    }
  }
</script>
<style lang="less" scoped>
  @import "../../assets/css/commom";
  .form_btn{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width:500px;
    margin: 0 auto;
    text-align: center;
  }
  .message_box{
    width:100%;
    padding:40px 50px;
    .comborder;
  }
  /deep/.ivu-input{
    border:none;
    background: #F1F1F1;
    border-radius: 4px;
  }
  /deep/.ivu-input-word-count{
    background: #F1F1F1;
  }
  /deep/textarea.ivu-input{
    height:300px;
  }
</style>



