<template>
  <div class="commom_style">
    <firstTitle name="消息管理"></firstTitle>
    <div class="input_box">
      <div class="search_box">
        <div class="commom_input">
          <label>消息标题:</label>
          <Input placeholder="请输入消息标题" style="margin-left: 10px;width:180px" v-model="postData.title"/>
        </div>
        <div class="commom_input">
          <label>消息内容:</label>
          <Input placeholder="请输入消息内容" style="margin-left: 10px;width:180px" v-model="postData.content"/>
        </div>
        <div class="commom_input">
          <label>消息类型:</label>
          <Select placeholder="请输入消息类型" style="margin-left: 10px;width:180px" v-model="postData.type" >
            <Option value="1">个人消息</Option>
            <Option value="2">全员公告</Option>
          </Select>
        </div>
        <div class="commom_input" style="margin-right: 20px;">
          <label>修改日期:</label>
          <DatePicker type="datetime" placeholder="起始时间"
                      style="width: 180px;margin-left: 10px;"
                      v-model="postData.updateTimeStart"
                      @on-change="selectStart"></DatePicker>
          &nbsp; — &nbsp;
          <DatePicker type="datetime"
                      placeholder="结束时间"
                      style="width: 180px"
                      @on-change="selectEnd"
                      v-model="postData.updateTimeEnd"></DatePicker>
        </div>
      </div>
      <div class="search_btn">
        <Button type="primary" style="margin-left: 20px" @click="findMessageList">搜索</Button>
        <Button type="primary" style="margin-left: 20px" @click="resetData">重置</Button>
      </div>
    </div>
    <div style="display: flex;flex-direction: row-reverse">
      <div class="is_reading" @click="deleteMessageIds()"><i class="read_icon"></i>批量删除</div>
      <div class="is_reading" style="margin-right: 10px" @click="$router.push('/detection/adminIndex/newAddMessage')"><i class="new_icon"></i>新建消息</div>
    </div>
    <Table stripe :columns="columns1" :data="data1" @on-selection-change="handleSelectRow">
      <template slot-scope="{ row, index }" slot="action">
        <span v-if="row.status ==1" class="table_btn" @click="$router.push('/detection/adminIndex/newAddMessage?id='+row.id)">修改</span>
        <span v-if="row.status ==1" class="table_btn" @click="deleteMessage(row.id)">删除</span>
        <span class="table_btn" @click="$router.push('/detection/adminIndex/messageDetail?id='+ row.id +'&role='+'admin')">查看</span>
        <span v-if="row.status ==1" class="table_btn" @click="publishMessage(row.id)">发布</span>
        <span v-if="row.status ==1" class="table_btn" @click="noReleaseMessage(row.id)">撤回</span>
        <span class="table_btn grey_btn" v-if="row.status !=1">修改</span>
        <span class="table_btn grey_btn" v-if="row.status !=1">删除</span>
        <span class="table_btn grey_btn" v-if="row.status !=1">发布</span>
        <span class="table_btn grey_btn" v-if="row.status !=1">撤回</span>
      </template>
    </Table>
    <splitPage :total="total" :page="postData.page"
               @psizeChange="psizeCha" @pageChange="pageCha"
               :rows="postData.rows"></splitPage>
  </div>
</template>
<script>
  import firstTitle from '../../components/firstTitle'
  import splitPage from '../../components/splitPage'
  export default {
    components:{firstTitle,splitPage},
    data () {
      return {
        columns1: [
          {
            type: 'selection',
            width: 35,
            align: 'center'
          },
          {
            title: '序号',
            type: 'index',
            align:'center',
            width:70,
          },
          {
            title: '消息标题',
            key: 'title',
            align:'center',
          },
          {
            title: '消息内容',
            key: 'content',
            align:'center',
            tooltip: true
          },
          {
            title: '发布人',
            key: 'releaseName',
            align:'center',
            tooltip: true
          },
          {
            title: '修改日期',
            key: 'updateTime',
            align:'center',
            tooltip: true
          },
          {
            title: '操作',
            slot: 'action',
            align:'center',
          }
        ],
        data1: [],
        total:'',
        postData:{
          page:1,
          rows:10,
          title:'',
          type:'',
          content:'',
          updateTimeStart:'',
          updateTimeEnd:'',
        },
        delStuData:[]
      }
    },
    methods:{
      resetData () {
        this.postData.title = ''
        this.postData.type = ''
        this.postData.content = ''
        this.postData.updateTimeStart = ''
        this.postData.updateTimeEnd = ''
      },
      // 批量选择
        handleSelectRow(selection){
          this.delStuData=[]
          for(var i=0;i<selection.length;i++){
            this.delStuData.push(selection[i].id)
          }
        },
      selectEnd (val){
        this.postData.updateTimeEnd = val
      },
      selectStart (val){
        this.postData.updateTimeStart = val
      },
      psizeCha(val) {
        this.postData.rows = val
        this.findMessageList()
      },
      pageCha(val) {
        this.postData.page = val
        this.findMessageList()
      },
      // 获得消息管理列表
      findMessageList () {
        this.$req().post(this.Api().findMessageList,this.postData, function(res) {
          if(res.data.code == 200){
            this.data1 = res.data.data.list
            this.total = res.data.data.total
          }else {
            this.$Message.error(res.data.msg)
          }
        })
      },
      // 删除
      deleteMessage (id){
        this.$Modal.confirm({
          title: '是否删除',
          onOk: () => {
            this.$req().get(this.Api().deleteMessageById+'?id='+id, function(res) {
              if(res.data.code == 200){
                this.$Message.info(res.data.msg)
                this.findMessageList()
              }else {
                this.$Message.error(res.data.msg)
              }
            })
          }
        });
      },
      // 批量删除
      deleteMessageIds () {
        if(!this.delStuData.length){
          this.$Message.info('请至少选择一条数据')
          return false
        }
        this.$req().post(this.Api().deleteMessageIds,{ids:this.delStuData}, function(res) {
          if(res.data.code == 200) {
            this.delStuData = []
            this.$Message.info(res.data.msg)
            this.findMessageList()
          }else {
            this.$Message.error(res.data.msg)
          }
        })
      },
      // 发息
      publishMessage (id) {
        this.$Modal.confirm({
          title: '是否发布',
          onOk: () => {
            this.$req().get(this.Api().publishMessage+'?id='+id, function(res) {
              if(res.data.code == 200){
                this.$Message.info(res.data.msg)
                this.findMessageList()
              }else {
                this.$Message.error(res.data.msg)
              }
            })
          }
        });
      },
      // 撤回消息
      noReleaseMessage (id) {
        this.$Modal.confirm({
          title: '是否撤回',
          onOk: () => {
            this.$req().get(this.Api().noReleaseMessage+'?id='+id, function(res) {
              if(res.data.code == 200){
                this.$Message.info(res.data.msg)
                this.findMessageList()
              }else {
                this.$Message.error(res.data.msg)
              }
            })
          }
        });
      }

    },
    mounted () {
      this.findMessageList()
    }
  }
</script>
<style lang="less" scoped>
  @import "../../assets/css/commom";
  .is_reading{
    width:85px;
    position: relative;
    cursor: pointer;
    text-align: right;
    .read_icon{
      width:14px;
      height:14px;
      background: url("../../assets/img/yidu.png") no-repeat;
      background-size: contain;
      position: absolute;
      left: 0px;
      top:50%;
      transform: translate(0,-50%);
    }
    .new_icon{
      width:14px;
      height:14px;
      background: url("../../assets/img/xinjian.png") no-repeat;
      background-size: contain;
      position: absolute;
      left: 0px;
      top:50%;
      transform: translate(0,-50%);
    }
    margin-bottom: 20px;
    color: #157AF7;
  }
  .input_box{
    .comborder;
    display: flex;
    flex-direction: row;
    padding:10px 0;
    margin-bottom: 20px;
    .search_box{
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      padding-top: 10px;
    }
    .search_btn{
      min-width:180px;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      border-left: 1px solid #D8D8D8;
      padding:0 20px 0 0px;
    }
  }
</style>



