<template>
  <div class="commom_style">
    <firstTitle name="消息通知"></firstTitle>
    <div class="input_box">
      <div class="search_box">
        <div class="commom_input">
          <label>消息标题:</label>
          <Input placeholder="请输入消息标题" style="margin-left: 10px;width:160px" v-model="postData.title"  @on-enter="messageNoticeList"/>
        </div>
        <div class="commom_input">
          <label>消息内容:</label>
          <Input placeholder="请输入消息内容" style="margin-left: 10px;width:160px" v-model="postData.content"  @on-enter="messageNoticeList"/>
        </div>
        <div class="commom_input">
          <label>消息类型:</label>
          <Select placeholder="请选择消息类型" style="margin-left: 10px;width:160px" v-model="postData.type" @on-change="messageNoticeList" >
            <Option value="1">个人消息</Option>
            <Option value="2">全员公告</Option>
          </Select>
        </div>
        <!--<div class="commom_input">-->
          <!--<label>发布人:</label>-->
          <!--<Input placeholder="请输入发布人" style="margin-left: 10px;width:160px" v-model="postData.releaseName"/>-->
        <!--</div>-->
        <div class="commom_input" style="margin-right: 20px;">
          <label>修改日期:</label>
          <DatePicker type="datetime" placeholder="起始时间"
                      style="width: 180px;margin-left: 10px;"
                      v-model="postData.updateTimeStart"
                      @on-change="selectStart"></DatePicker>
          &nbsp; — &nbsp;
          <DatePicker type="datetime"
                      placeholder="结束时间"
                      style="width: 180px"
                      @on-change="selectEnd"
                      v-model="postData.updateTimeEnd"></DatePicker>
        </div>
      </div>
     <div class="search_btn">
       <Button type="primary" style="margin-left: 20px" @click="messageNoticeList">搜索</Button>
       <Button type="primary" style="margin-left: 20px" @click="resetData">重置</Button>
     </div>
    </div>
    <div class="is_reading" @click="lookMessage()"><i class="read_icon"></i>批量已读</div>
    <Table stripe :columns="columns1" :data="data1" @on-selection-change="handleSelectRow">
      <template slot-scope="{ row, index }" slot="action">
        <span class="table_btn" @click="$router.push('/detection/applyIndex/messageDetail?id='+ row.id + '&status=' + row.status)">详情</span>
      </template>
    </Table>
    <splitPage :total="total" :page="postData.page"
               @psizeChange="psizeCha" @pageChange="pageCha"
               :rows="postData.rows"></splitPage>
  </div>
</template>
<script>
  import firstTitle from '../../components/firstTitle'
  import splitPage from '../../components/splitPage'
  export default {
    components:{firstTitle,splitPage},
    data () {
      return {
        columns1: [
          {
            type: 'selection',
            width: 35,
            align: 'center'
          },
          {
            title: '序号',
            type: 'index',
            align:'center',
            width:70,
          },
          {
            title: '消息标题',
            key: 'title',
            align:'center',
            width:100,
            render: (h,params) => {
              return h('div', {
                style:{
                  'display': 'flex',
                  'align-items': 'center',
                }
              },params.row.status == 2?[
                h('img',{
                  attrs:{
                    src: require('../../assets/img/tongzhitixing.png')
                  },
                }),
                h('span',{
                  style:{
                    'display': 'inline-block',
                    'padding-left':'10px'
                  }
                },params.row.title)
              ]:params.row.title)
            }
          },
          {
            title: '消息内容',
            key: 'content',
            align:'center',
            tooltip: true
          },
          {
            title: '消息类型',
            key: 'typeName',
            align:'center',
            tooltip: true
          },
          {
            title: '发布人',
            key: 'releaseName',
            align:'center',
            tooltip: true
          },
          {
            title: '修改日期',
            key: 'updateTime',
            align:'center',
            tooltip: true
          },
          {
            title: '操作',
            slot: 'action',
            align:'center',
            width:80
          }
        ],
        data1: [],
        postData:{
          page:1,
          rows:10,
          title:'',
          content:'',
          type:'',
          updateTimeStart:'',
          updateTimeEnd:'',
        },
        total:'',
        readDatas:[]
      }
    },
    methods:{
      resetData () {
        this.postData.title = ''
        this.postData.content = ''
        this.postData.type = ''
        this.postData.updateTimeStart = ''
        this.postData.updateTimeEnd = ''
      },
      psizeCha(val) {
        this.postData.rows = val
        this.messageNoticeList()
      },
      pageCha(val) {
        this.postData.page = val
        this.messageNoticeList()
      },
      selectEnd (val){
        this.postData.updateTimeEnd = val
      },
      selectStart (val){
        this.postData.updateTimeStart = val
      },
      messageNoticeList () {
        this.$req().post(this.Api().messageNoticeList,this.postData, function(res) {
          if(res.data.code == 200) {
            this.data1 = res.data.data.list
            this.total = res.data.data.total
          }else {
            this.$Message.error(res.data.msg)
          }
        })
      },
      // 批量选择
      handleSelectRow(selection){
        this.readDatas=[]
        for(var i=0;i<selection.length;i++){
          this.readDatas.push(selection[i].id + ',' + selection[i].status)
        }
      },
      // 批量已读
      lookMessage () {
        if(!this.readDatas.length){
          this.$Message.info('请至少选择一条数据')
          return false
        }
        this.$req().post(this.Api().batchLooks,{ids:this.readDatas}, function(res) {
          if(res.data.code == 200) {
            this.readDatas = []
            this.$Message.info(res.data.msg)
            this.messageNoticeList()
          }else {
            this.$Message.error(res.data.msg)
          }
        })
      }
    },
    mounted () {
      this.messageNoticeList()
    }
  }
</script>
<style lang="less" scoped>
  @import "../../assets/css/commom";
  .is_reading{
    position: relative;
    cursor: pointer;
    .read_icon{
      width:14px;
      height:14px;
      background: url("../../assets/img/yidu.png") no-repeat;
      background-size: contain;
      position: absolute;
      right: 70px;
      top:50%;
      transform: translate(0,-50%);
    }
    display: flex;
    justify-content: flex-end;
    margin-bottom: 20px;
    color: #157AF7;
  }
  .input_box{
    .comborder;
    display: flex;
    flex-direction: row;
    padding:10px 0;
    margin-bottom: 20px;
    .search_box{
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      padding-top: 10px;
    }
    .search_btn{
      min-width:180px;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      border-left: 1px solid #D8D8D8;
      padding:0 20px 0 0px;
    }
  }
</style>



