<template>
  <div class="commom_style">
    <firstTitle :name="$route.query.number?'首页':'检测审核'"></firstTitle>
    <div class="input_box">
      <div class="search_box">
        <div class="commom_input">
          <label>工单编号:</label>
          <Input placeholder="请输入工单编号" style="margin-left: 10px;width:180px" v-model="postData.number"/>
        </div>
        <div class="commom_input">
          <label>工单状态:</label>
          <Select placeholder="请选择工单状态" style="margin-left: 10px;width:180px" v-model="postData.status" >
            <Option v-for="(i,j) in orderStatusList" :value="i.value">{{i.text}}</Option>
          </Select>
        </div>
        <div class="commom_input" style="margin-right: 20px;">
          <label>修改日期:</label>
          <DatePicker type="datetime" placeholder="起始时间"
                      style="width: 180px;margin-left: 10px;"
                      v-model="postData.updateTimeStart"
                      @on-change="selectStart"></DatePicker>
          &nbsp; — &nbsp;
          <DatePicker type="datetime"
                      placeholder="结束时间"
                      style="width: 180px"
                      @on-change="selectEnd"
                      v-model="postData.updateTimeEnd"></DatePicker>
        </div>
      </div>
      <div class="search_btn">
        <Button type="primary" style="margin-left: 20px" @click="findOrderExamine">搜索</Button>
        <Button type="primary" style="margin-left: 20px" @click="resetValue">重置</Button>
      </div>
    </div>
    <Table stripe :columns="columns1" :data="data1">
      <template slot-scope="{ row, index }" slot="action">
        <span class="table_btn" @click="$router.push('/detection/adminIndex/infoCheckDetail?id='+row.id + '&role=admin')">信息审阅</span>
        <span class="table_btn" @click="jumpCheck(row)">本体检测</span>
        <span class="table_btn" @click="infoCheckPass(row.id)" v-if="row.status == 2 || row.status == 4 ">审核通过</span>
        <span class="table_btn" @click="infoNoPass(row.id)" v-if="row.status == 2">申请驳回</span>

        <span class="table_btn grey_btn" v-if="row.status == 3 || row.status == 5 ">审核通过</span>
        <span class="table_btn grey_btn" v-if="row.status != 2">申请驳回</span>
        <span class="table_btn" v-if="row.status == 4" @click="taskUserList(row.id)">任务派单</span>
        <span class="table_btn grey_btn" v-if="row.status != 4">任务派单</span>
      </template>
    </Table>
    <splitPage :total="total" :page="postData.page"
               @psizeChange="psizeCha" @pageChange="pageCha"
               :rows="postData.rows"></splitPage>
    <commomComfirm ref="myComfirm" titleName="任务派单" modalWidth="1300">
      <Table stripe :columns="taskColumns" :data="taskList">
        <template slot-scope="{ row, index }" slot="action">
          <span class="table_btn" @click="deliverTask(row.sysUser.id)">派单</span>
        </template>
      </Table>
      <splitPage :total="taskTotal" :page="taskData.page"
                 @psizeChange="psizeChaTask" @pageChange="pageChaTask"
                 :rows="taskData.rows"></splitPage>
    </commomComfirm>
  </div>
</template>
<script>
  import firstTitle from '../../components/firstTitle'
  import splitPage from '../../components/splitPage'
  import commomComfirm from '../../components/comomComfirm'
  export default {
    components:{firstTitle,splitPage,commomComfirm},
    data () {
      return {
        columns1: [
          {
            title: '序号',
            type: 'index',
            align:'center',
            width:70,
          },
          {
            title: '工单编号',
            key: 'number',
            align:'center',
            tooltip: true,
          },
          {
            title: '工单类型',
            key: 'typeName',
            align:'center',
            tooltip: true
          },
          {
            title: '工单状态',
            key: 'statusName',
            align:'center',
            tooltip: true
          },
          {
            title: '修改日期',
            key: 'updateTime',
            align:'center',
            tooltip: true
          },
          {
            title: '操作',
            slot: 'action',
            align:'center',
            minWidth:80
          }
        ],
        taskColumns:[
          {
            title: '序号',
            type: 'index',
            align:'center',
            width:70,
          },
          {
            title: '姓名',
            key: 'username',
            align:'center',
            render: (h,params) => {
              return h('div', params.row.sysUser.username)
            }
          },
          {
            title: '性别',
            key: 'gender',
            align:'center',
            tooltip: true,
            render: (h,params) => {
              return h('div', params.row.sysUser.genderName)
            }
          },
          {
            title: '联系号码',
            key: 'telephone',
            align:'center',
            tooltip: true,
            render: (h,params) => {
              return h('div', params.row.sysUser.telephone)
            }
          },
          {
            title: '历史完成',
            key: 'finishCount',
            align:'center',
            tooltip: true
          },
          {
            title: '当前任务',
            key: 'runCount',
            align:'center',
            tooltip: true
          },
          {
            title: '操作',
            slot: 'action',
            align:'center',
            width:80
          }
        ],
        data1:[],
        taskList:[],
        total:'',
        postData:{
          page:1,
          rows:10,
          number:'',
          status:'',
          updateTimeStart:'',
          updateTimeEnd:'',
        },
        orderStatusList:[],
        taskData:{
          page:1,
          rows:10
        },
        taskTotal:'',
        orderId:''

      }
    },
    watch:{
      '$route.query.number' (o){
        if(!o){
          this.postData.number = ''
          this.postData.status = ''
          this.postData.updateTimeStart = ''
          this.postData.updateTimeEnd = ''
          this.findOrderExamine()
        }
      }
    },
    methods:{
      jumpCheck(row){
        localStorage.saveCheck = JSON.stringify(row)
        this.$router.push('/detection/adminIndex/ontologyDetection')
      },
      // 重置
      resetValue () {
        this.postData.number = ''
        this.postData.status = ''
        this.postData.updateTimeStart = ''
        this.postData.updateTimeEnd = ''
      },
      selectEnd (val){
        this.postData.updateTimeEnd = val
      },
      selectStart (val){
        this.postData.updateTimeStart = val
      },
      psizeCha(val) {
        this.postData.rows = val
        this.findOrderExamine()
      },
      pageCha(val) {
        this.postData.page = val
        this.findOrderExamine()
      },
      psizeChaTask(val) {
        this.taskData.rows = val
        this.taskUserList(this.orderId)
      },
      pageChaTask(val) {
        this.taskData.page = val
        this.taskUserList(this.orderId)
      },
      // 审核通过
      infoCheckPass (id) {
        this.$Modal.confirm({
          title: '是否审核通过',
          onOk: () => {
            this.$req().get(this.Api().infoCheckPass+'?id='+id, function(res) {
              if(res.data.code == 200){
                this.$Message.info(res.data.msg)
                this.findOrderExamine()
              }else {
                this.$Message.error(res.data.msg)
              }
            })
          }
        });
      },
      // 申请驳回
      infoNoPass (id) {
        this.$Modal.confirm({
          title: '是否申请驳回',
          onOk: () => {
            this.$req().post(this.Api().infoNoPass,{id:id}, function(res) {
              if(res.data.code == 200){
                this.$Message.info(res.data.msg)
                this.findOrderExamine()
              }else {
                this.$Message.error(res.data.msg)
              }
            })
          }
        });
      },
      findOrderExamine () {
        this.$req().post(this.Api().findOrderExamine,this.postData, function(res) {
          if(res.data.code == 200) {
            this.data1 = res.data.data.list
            this.total = res.data.data.total
          }else {
            this.$Message.error(res.data.msg)
          }
        })
      },
      //工单状态
      getOrderStatusList(){
        this.$req().get(this.Api().getOrderStatusList+'?parentId='+ 660, function(res) {
          if(res.data.code == 200){
           this.orderStatusList = res.data.data
          }else {
            this.$Message.error(res.data.msg)
          }
        })
      },
      // 获得派单列表
      taskUserList (id) {
        this.orderId = id
        this.$req().post(this.Api().taskUserList,this.taskData, function(res) {
          if(res.data.code == 200){
            this.$refs.myComfirm.modal = true
            this.taskList = res.data.data.list
            this.taskTotal = res.data.data.taskTotal
          }else {
            this.$Message.error(res.data.msg)
          }
        })
      },
      // 派单
      deliverTask (id) {
        this.$req().get(this.Api().deliverTask+'?orderId='+this.orderId
          +'&receivedId=' + id
          , function(res) {
          if(res.data.code == 200){
            this.$refs.myComfirm.modal = false
            this.$Message.info(res.data.msg)
            this.findOrderExamine()
          }else {
            this.$Message.error(res.data.msg)
          }
        })
      }
    },
    mounted () {
      this.findOrderExamine()
      this.getOrderStatusList()
      if(this.$route.query.number) {
        this.postData.number = this.$route.query.number
        this.findOrderExamine()
      }
    }
  }
</script>
<style lang="less" scoped>
  @import "../../assets/css/commom";
  .input_box{
    .comborder;
    display: flex;
    flex-direction: row;
    margin-bottom: 20px;
    padding:10px 0;
    .search_box{
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      padding-top:10px;
    }
    .search_btn{
      min-width:180px;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      border-left: 1px solid #D8D8D8;
      padding:0 20px 0 0px;
      margin-left: 20px;
    }
  }
</style>



