<template>
  <div class="commom_style">
    <SecendTitle name1="检测审核" name2="威胁扫描"></SecendTitle>
    <div class="detect_box">
      <div class="commom_box">
        <p>威胁扫描</p>
       <div class="detect_title">
         <div><span>工单编号</span><span>{{obj.number}}</span></div>
         <div><span>检测体附件</span><span style="color: #157AF7;">{{obj.checkBodyName}}</span></div>
       </div>
        <div class="detect_select">
          <div style="font-size: 14px;color: #333333;padding:20px 30px;">检测类型</div>
          <ul>
            <CheckboxGroup v-model="mulSelect">
            <li v-for="(item,index) in checkData">
              <div>
                <img style="margin-right: 16px" :src="item.chName | filterImg" alt="">
                {{item.chName}}
              </div>
              <div><Checkbox :label="item.type"></Checkbox></div>
            </li>
            </CheckboxGroup>
          </ul>
        </div>
        <Button type="primary" class="back_btn" @click="startCheck()">开始检测</Button>
      </div>
      <div class="commom_box" style="padding-top: 20px;">
        <div class="detect_select" style="height:90% !important;">
          <div style="font-size: 14px;color: #333333;padding:20px 30px;">检测结果</div>
          <div>
            <div v-for="(item,index) in testResult" class="result_list">
              <div v-if="!item.yara_check.length">
                <div>{{item.rule_chname}}:</div>
                <div>检测完毕，没有发现恶意代码</div>
              </div>
              <div v-if="item.yara_check.length">
                <div>{{item.rule_chname}}:</div>
                <div v-for="(i,j) in item.yara_check">
                 <span v-for="(k,j) in i" style="display: block;">{{j}}:{{k}}</span>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="commom_back_btn" @click="$router.go(-1)">返回列表</div>
      </div>
      <Spin fix v-if="isShowLoading" slot>
        <Progress :percent="per" status="active" :stroke-width="13" />
        <span>上传中...</span>
      </Spin>
    </div>
  </div>
</template>
<script>
  import SecendTitle from "../../components/secendTitle";
  export default {
    components: {SecendTitle},
    data () {
      return {
        isShowLoading:false,
        per:0,
        checkData:[],
        mulSelect:[], // 多选
        obj:{},
        testResult:[]
      }
    },
    filters: {
      filterImg (val) {
        switch (val) {
          case '漏洞利用套件':
            return require('../../assets/img/loudongliyong.png');
          case '恶意文档病毒':
            return require('../../assets/img/eyi.png');
          case '加密潜在威胁':
            return require('../../assets/img/jiami.png');
          case '反调试/反沙箱类潜在威胁':
            return require('../../assets/img/tiaoshi.png');
          case 'CVE漏洞利用类潜在威胁':
            return require('../../assets/img/cve.png');
          case '网页脚本病毒':
            return require('../../assets/img/wangyejiaoben.png');
          case '邮箱病毒':
            return require('../../assets/img/youxiangbingdu.png');
          default:
            return false
        }

      }
    },
    methods:{
      checkBody (orderId) {
        this.$req().get(this.Api().checkBody+'?id=' + orderId, function(res) {
          if(res.data.code != 200){
            this.$Message.error(res.data.msg)
          }
        })
      },
      checkBugList () {
        this.$req().get(this.Api().checkBugList, function(res) {
          if(res.data.code == 200){
            this.checkData = res.data.data
          }else {
            this.$Message.error(res.data.msg)
          }
        })
      },
      startCheck () {
        if(!this.mulSelect.length){
          this.$Message.info('请至少选择一项')
          return false
        }
        this.isShowLoading = true
        this.per = 100 - this.mulSelect.length * 10
        this.$req().post(this.Api().startCheck, {
          order_id:this.obj.id,
          types:this.mulSelect.join(','),
          filePath:this.obj.checkBodyUrl,
        },function(res) {
          if(res.data.code == 200){
            this.checkBody(this.obj.id)
            this.testResult = res.data.data
            this.mulSelect = []
            this.per = 100
            let _this = this
            setTimeout(function () {
              _this.isShowLoading = false
            },1000)
          }else {
            this.$Message.error(res.data.msg)
          }
        })
      }
    },
    mounted () {
      this.obj = JSON.parse(localStorage.saveCheck)
      this.checkBugList()
    }
  }
</script>
<style lang="less" scoped>
  @import "../../assets/css/commom";
  /deep/ .ivu-checkbox +span{
    display: none !important;
  }
  /deep/ .ivu-progress-outer {
    width: 280px;
  }

  /deep/ .ivu-progress-inner {
    background-color: #f9f9f9;
  }

  /deep/ .ivu-progress-text {
    color: #fff;
  }
  .detect_box{
    display: flex;
    flex-direction: row;
    .commom_box{
      width:50%;
      padding:10px 20px;
      height:800px;
      .comborder;
      &:last-child{
        margin-left: 20px;
      }
      p{
        font-size: 18px;
        color: #333333;
        font-weight: bold;
      }
      .detect_title{
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        margin:30px 0;
        span:first-child{
          font-size: 18px;
          color: #ADADAD;
        }
        span:last-child{
          margin-left: 10px;
          font-size: 18px;
          color: #333333;
        }
      }

      .detect_select{
        background: #F8F9FB;
        width:100%;
        height:74%;
        overflow: auto;
        ul{
          li{
            margin:0 auto;
            width:358px;
            height:60px;
            line-height: 60px;
            border-bottom: 1px solid #D8D8D8;
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            font-size: 14px;
            color: #333333;
            div:first-child{
              display: flex;
              align-items: center;
            }
          }
        }
      }
    }
  }
  .result_list{
    margin-bottom: 30px;
    div{
      margin-left: 20px;
      span{
        margin-bottom: 10px;
      }
    }
  }
</style>



