<template>
  <div class="commom_style">
    <firstTitle name="首页"></firstTitle>
    <div class="sheet_list">
      <div class="sheet_box">
        <img src="../../assets/img/lishi.png" alt="">
        <div>
          <span>{{countObj.allCount}}</span><span>历史审核申请</span>
        </div>
      </div>
      <div class="sheet_box">
        <img src="../../assets/img/xinzeng.png" alt="">
        <div>
          <span>{{countObj.yesterdayCount}}</span><span>昨日新增申请</span>
        </div>
      </div>
      <div class="sheet_box">
        <img src="../../assets/img/jinxingzhong.png" alt="">
        <div>
          <span>{{countObj.runCount}}</span><span>正在进行任务</span>
        </div>
      </div>
      <div class="sheet_box">
        <img src="../../assets/img/yiwancheng.png" alt="">
        <div>
          <span>{{countObj.finishCount}}</span><span>完成审核总计</span>
        </div>
      </div>
    </div>
    <div style="margin: 20px 0 5px;">
      进行中的任务
    </div>
    <Table stripe :columns="columns1" :data="data1">
      <template slot-scope="{ row, index }" slot="action">
        <span class="table_btn" @click="$router.push('/detection/adminIndex/testAuditing?number=' + row.number)">任务查看</span>
      </template>
    </Table>
    <splitPage></splitPage>
  </div>
</template>
<script>
  import firstTitle from '../../components/firstTitle'
  import splitPage from '../../components/splitPage'
  export default {
    components:{firstTitle,splitPage},
    data () {
      return {
        columns1: [
          {
            title: '序号',
            type: 'index',
            align:'center',
            width:70,
          },
          {
            title: '工单编号',
            key: 'number',
            align:'center',
            tooltip: true,
          },
          {
            title: '检测类型',
            key: 'typeName',
            align:'center',
            tooltip: true
          },
          {
            title: '最后操作时间',
            key: 'updateTime',
            align:'center',
            tooltip: true
          },
          {
            title: '操作',
            slot: 'action',
            align:'center',
            width:100
          }
        ],
        data1: [],
        countObj:{},

      }
    },
    methods:{
      adminHomePage () {
        this.$req().get(this.Api().adminHomePage, function(res) {
          if(res.data.code == 200){
            this.countObj = res.data.data.count
            this.data1 = res.data.data.orders
          }else {
            this.$Message.error(res.data.msg)
          }
        })
      }
    },
    mounted () {
      this.adminHomePage()
    }
  }
</script>
<style lang="less">
  @import "../../assets/css/commom";
  .sheet_list{
    .comborder;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width:100%;
    height:200px;
    padding: 0 80px;
    .sheet_box{
      display: flex;
      flex-direction: row;
      align-items: center;
      div{
        display: flex;
        flex-direction: column;
        margin-left: 13px;
        span:first-child{
          font-size: 36px;
          color: #157AF7;
        }
        span:last-child{
          font-size: 18px;
          color: #333333;
        }
      }
    }
  }
</style>



