<template>
  <div class="commom_style">
    <SecendTitle :name1="$route.query.name" :name2="$route.query.id?'编辑工单':'新建工单'"></SecendTitle>
    <div class="work_info">
      <div class="work_title">基本信息</div>
      <div class="work_input">
        <div class="title_list">
          <div v-for="(i,j) in postData.option">
            <Input placeholder="请输入标题名称"  style="width: 206px;" v-model="i.name"/>
            <Input placeholder="请输入详细内容" style="width: 500px; margin-left: 20px" v-model="i.content"/>
            <img src="../../assets/img/xinjianbig.png" alt="" @click="addNewInput()" v-if="j==0">
          </div>
        </div>
        <div class="upload_box">
          <div>
            <span>检测体附件：</span>
            <span>
              <Upload action="" style="display: inline-block;" :before-upload="(file)=>{handleUploadFile(file,1);return false}" >
               <i class="upload_icon" v-if="!this.testFile.fileName"></i>
                <b v-if="this.testFile.fileName">{{this.testFile.fileName}}</b>
              </Upload>
            </span>
          </div>
          <div>
            <span>文档附件：</span>
            <span>
             <Upload action="" style="display: inline-block;" :before-upload="(file)=>{handleUploadFile(file,2);return false}" >
               <i class="upload_icon" v-if="!this.docFile.fileName"></i>
                <b v-if="this.docFile.fileName">{{this.docFile.fileName}}</b>
              </Upload>
            </span>
          </div>
          <div>
            <span>图片附件：</span>
            <span>
               <Upload action="" style="display: inline-block;" :before-upload="(file)=>{handleUploadFile(file,3);return false}" >
               <i class="upload_icon" v-if="!this.imgFile.fileName"></i>
                 <b v-if="this.imgFile.fileName">{{this.imgFile.fileName}}</b>
              </Upload>
            </span>
          </div>
        </div>
      </div>
      <div class="form_btn">
        <Button type="primary" style="background: #687D97;" class="back_btn" @click="$router.go(-1)">取消</Button>
        <Button type="primary" class="back_btn" @click="saveOrder()">保存</Button>
      </div>
    </div>
  </div>
</template>
<script>
  import SecendTitle from "../../components/secendTitle";
  import axios from 'axios'
  export default {
    components: {SecendTitle},
    data () {
      return {
        testFile:{
          file:'',
          fileName:''
        },
        docFile:{
          file:'',
          fileName:''
        },
        imgFile:{
          file:'',
          fileName:''
        },
        postData:{
          option:[
            {
              name:'',
              content:''
            }
          ],
          type:this.$route.query.type,
        },
        uploadId:''
      }
    },
    methods:{
      addNewInput () {
        this.postData.option.push(
          {
            name:'',
            content:''
          }
        )
      },
      // 保存工单
      saveOrder () {
        let param = {}
        let obj = {}
        for(let i = 0;i<this.postData.option.length;i++){
          this.$set(obj, this.postData.option[i].name,this.postData.option[i].content)
        }
        this.$set(param,'option',JSON.stringify(obj))
        this.$set(param,'type',this.$route.query.type)
        this.$set(param,'id',this.uploadId)
        this.$req().post(this.Api().saveOrder,param, function(res) {
          if(res.data.code == 200){
            this.$router.go(-1)
          }else {
            this.$Message.error(res.data.msg)
          }

        })
      },
      uploadFile (file,name) {
        let formData = new window.FormData()
        formData.append('files', file)
        formData.append(name, file.name)
        formData.append('id',this.uploadId)
        formData.append('type',this.$route.query.type)
        let headersDatas = {
          headers: {
            'Authorization': 'Bearer ' + localStorage.token
          }
        }
        let _this = this
        axios.post(this.Api().uploadOrder, formData,headersDatas).then(function (res) {
          if (res.data.code == 200) {
             _this.uploadId = res.data.data.id
          }else {
            _this.$Message.error(res.data.msg)
          }
        })
      },
      handleUploadFile(file,uploadType) {
        switch (uploadType) {
          case 1:
            this.testFile.file = file;
            this.testFile.fileName = file.name
            this.uploadFile(file,'checkBodyName')
            return false
          case 2:
            this.docFile.file = file;
            this.docFile.fileName = file.name
            this.uploadFile(file,'wordName')
            return false
          case 3:
            this.imgFile.file = file;
            this.imgFile.fileName = file.name
            this.uploadFile(file,'imgName')
            return false
          default:
            return false
        }
      },
      // 获得工单详情
      getOrderDetail () {
        this.$req().get(this.Api().getOrderDetail +
          '?id=' + this.$route.query.id,function(res) {
            if(res.data.code == 200) {
              if(res.data.data.option){
                this.postData.option = []
                let obj =  JSON.parse(res.data.data.option)
                for (let i in obj){
                  this.postData.option.push(
                    {
                      name:i,
                      content:obj[i]
                    }
                  )
                }
              }
              this.testFile.fileName = res.data.data.checkBodyName
              this.docFile.fileName = res.data.data.wordName
              this.imgFile.fileName = res.data.data.imgName
              this.uploadId = res.data.data.id
            } else {
              this.$Message.error(res.data.msg);
            }
          })
      }
    },
    mounted () {
      if(this.$route.query.id){
        this.getOrderDetail()
      }
    }
  }
</script>
<style lang="less" scoped>
  @import "../../assets/css/commom";
  .form_btn{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width:500px;
    margin:60px auto 0;
    text-align: center;
  }
  .work_info{
    padding:20px 30px;
    .comborder;
    .work_title{
      font-size: 18px;
      color: #333333;
      font-weight: bold;
    }
    .work_input{
      margin:40px 0 40px 60px;
      .upload_box{
        margin: 20px 0;
        div{
          margin-bottom: 20px;
          display: flex;
          flex-direction: row;
          align-items: center;
          span:first-child{
            font-size: 14px;
            color: #333333;
            min-width:84px;
          }
          span:last-child{
            height:40px;
            line-height: 40px;
            width:100%;
            position: relative;
            .upload_icon{
              width:150px;
              height:40px;
              background: url("../../assets/img/shangchuananniu.png") no-repeat;
              background-size: contain;
              position: absolute;
              top:0;
              left:0px;
            }
            b{
              font-weight: normal;
              color: red;
            }
          }
        }
      }
    }
    .title_list{
      display: flex;
      flex-direction: column;
      div{
        margin-bottom: 10px;
      }
      div:first-child{
        position: relative;
        img{
          width:18px;
          height:auto;
          margin-left: 20px;
          position: absolute;
          left:730px;
          top:7px;
        }
      }
    }
  }
</style>



