import Vue from 'vue'
import Router from 'vue-router'
import actuatingIndex from '../views/actuatingStation/actuatingIndex'
import actuatingHomePage from '../views/actuatingStation/homePage'
import taskSheet from '../views/actuatingStation/taskSheet'
import taskSheetDetail from '../views/actuatingStation/taskSheetDetail'
import messageNotice from '../views/actuatingStation/messageNotice'
import messageDetail from '../views/actuatingStation/messageDetail'

import adminIndex from '../views/admin/adminIndex'
import adminHomePage from '../views/admin/homePage'
import processSupervision from '../views/admin/processSupervision'
import testAuditing from '../views/admin/testAuditing'
import messageManage from '../views/admin/messageManage'
import newAddMessage from '../views/admin/newAddMessage'
import ontologyDetection from  '../views/admin/ontologyDetection'

import applyIndex from '../views/application/applyIndex'
import applyHomePage from '../views/application/applyHomePage'
import tabTestApply from '../views/application/tabTestApply'
import newWork from  '../views/application/newWork'
import login from '../views/commom/login'
Vue.use(Router)

let routes = [
  // 执行端
  {
    path: '/detection/actuatingIndex',
    name: 'actuatingIndex',
    component: actuatingIndex,
    children:[
      {
        path: '/detection/actuatingIndex/actuatingHomePage',
        component: actuatingHomePage
      },
      {
        path: '/detection/actuatingIndex/taskSheet',
        component: taskSheet
      },
      {
        path: '/detection/actuatingIndex/taskSheetDetail',
        component: taskSheetDetail
      },
      {
        path: '/detection/actuatingIndex/messageNotice',
        component: messageNotice
      },
      {
        path: '/detection/applyIndex/messageDetail',
        component: messageDetail
      },

    ]
  },


  // 管理端
  {
    path: '/detection/adminIndex',
    name: 'adminIndex',
    component: adminIndex,
    children:[
      {
        path: '/detection/adminIndex/adminHomePage',
        component: adminHomePage
      },
      {
        path: '/detection/adminIndex/testAuditing',
        component: testAuditing
      },
      {
        path: '/detection/adminIndex/processSupervision',
        component: processSupervision
      },
      {
        path: '/detection/adminIndex/messageManage',
        component: messageManage
      },
      {
        path: '/detection/adminIndex/newAddMessage',
        component: newAddMessage
      },
      {
        path: '/detection/adminIndex/ontologyDetection',
        component: ontologyDetection
      },
      {
        path: '/detection/adminIndex/messageDetail',
        component: messageDetail
      },
      {
        path: '/detection/adminIndex/infoCheckDetail',
        component: taskSheetDetail
      },
    ]
  },

  // 申请端首页
  {
    path: '/detection/applyIndex',
    name: applyIndex,
    component: applyIndex
  },
  // 申请端主页
  {
    path: '/detection/applyHomePage',
    name: applyHomePage,
    component: applyHomePage,
    children:[
      {
        path: '/detection/applyHomePage/tabTestApply',
        component: tabTestApply
      },
      {
        path: '/detection/applyHomePage/newWork',
        component: newWork
      }
    ]
  },
  // 登录
  {
    path: '/login',
    name: login,
    component: login
  },

  // {
  //   path: '/',
  //   redirect: '/applyIndex'
  // },
]
// 创建路由对象
const mRouter = new Router({
  routes
}, )
export default mRouter



